import { Component, OnInit, ViewEncapsulation, ViewChild ,AfterViewInit} from '@angular/core';
import {AdminService} from '../../services/admin/admin.service';
import {AuthService} from '../../services/auth/auth.service';
import {ManagementService} from '../../services/management/management.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { GroupeActionComponent } from './groupe-action/groupe-action.component';
import {merge, of as observableOf} from 'rxjs';
import {map, startWith, switchMap} from 'rxjs/operators';
import * as introJs from 'intro.js/intro.js';
import { Router } from '@angular/router';


export interface Aclient  {
  id: string;
  fname: string;
  lname: string;
  adress: string;
  email: string;
}

export interface GroupesAPI {
  items: any[];
  total_count: number;
}


@Component({
  selector: 'app-groupes',
  templateUrl: './groupes.component.html',
  styleUrls: ['./groupes.component.scss']
})





export class GroupesComponent implements OnInit,AfterViewInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  aclientList: Aclient[];
  public groupes: any;

  MyDataSource: any;
  displayedColumns: string[] = ['groupename', 'action'];
  data = [] ;
  tableSort = -1;
  totalDocs = 0 ;
  filter = '';
  isLoadingResults = true;
  public deleteId: string ;
  constructor(
    private adminService: AdminService,
    public authservice: AuthService,
    private snackBar: MatSnackBar,
    private dialog: MatDialog,
    private router: Router
    ) { }

  ngOnInit() {
    if(this.authservice.introjs){      
      setTimeout(() =>{
        let el = document.getElementById("step1");
        el.scrollIntoView();
        this.authservice.introJS.goToStep(5).start();
      }, 500);
    }
   this.getData('no-query');
  }


  ngAfterViewInit() {
    this.sort.sortChange.subscribe(() =>{    
        this.tableSort *= -1 ;
    });
    this.loadData('no-query');
  }
  
  loadData(query){
    merge(this.sort.sortChange, this.paginator.page)
    .pipe(
      startWith({}),
      switchMap(() => {
        this.isLoadingResults = true;
        return this.getData(query);
      }),
      map(data => {
        this.isLoadingResults = false;     
        this.totalDocs = data.totalDocs ;
        return data.docs;
      })
    ).subscribe(data => this.MyDataSource = new MatTableDataSource(data));
  }

  getData(query){
    console.log(this.paginator.pageIndex);
    return this.adminService.getGroupesToTable(this.paginator.pageIndex+1,this.paginator.pageSize,this.tableSort,query);
  }




  togglModal(id) {
    this.deleteId = id;
  }

  delete(id) {
    this.adminService.deleteGroupe(id)
      .subscribe(res => {
        this.getData('no-query');
        const element: HTMLElement = document.getElementById('hideModal') as HTMLElement;
          element.click();
          this.snackBar.open('Groupe supprim avec succs', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        }, (err) => {
          console.log(err);
        }
      );
  }


  openForm(groupeId = null) {
    const configDialog = new MatDialogConfig();
    configDialog.width = '65%';
    configDialog.panelClass = 'custom-dialog-container' ;
    configDialog.height = "100%"
    configDialog.data = {
      id: groupeId,
  };
  const dialogRef = this.dialog.open(GroupeActionComponent, configDialog);
    dialogRef.afterClosed().subscribe(result => {
      this.getData('no-query');
      this.sort.sortChange.subscribe(() =>{    
        this.tableSort *= -1 ;
    });
    this.loadData('no-query');
  });
  this.router.events
    .subscribe(() => {
      dialogRef.close();
    });

  }

 

  applyFilter(filterValue: string) {
    if(filterValue == '')
    filterValue = 'no-query';
    this.paginator.pageIndex = 0;
    this.loadData(filterValue);

  }

}
