import { Component, OnInit, ViewChild, ChangeDetectorRef, ElementRef } from '@angular/core';
import {AdminService} from '../../services/admin/admin.service' ;
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { environment } from '../../../environments/environment' ;
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { FileUploader } from 'ng2-file-upload';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';


const uri = environment.uri;


@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss']
})
export class feedbackComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild('closeModal', {static: true} ) closeModal: ElementRef;
  uploader: FileUploader = new FileUploader({url: uri, removeAfterUpload: true , allowedFileType: ['image', 'doc', 'pdf', 'xls', 'ppt']});
  filePreviewPath: SafeUrl;
  hasBaseDropZoneOver:boolean = false;
  hasAnotherDropZoneOver:boolean = false;
  submited = false;
  removing = false;
  form: FormGroup ;
  uri = environment.baseUrl;
  mypermissions: any[];
  MyDataSource: any;
  displayedColumns: string[] = ['Libelle','Description','Date','Type','Etat'];
  docList = [] ;

confirmationId :string ;

  constructor(
              private adminService: AdminService,
              private fb: FormBuilder,
              private changeDetectorRef: ChangeDetectorRef,
              private sanitizer: DomSanitizer,          
              private snackBar: MatSnackBar,) { 
    
                this.uploader.onBeforeUploadItem = (item) => {
                  item.withCredentials = false;             
                 }
                  this.uploader.onAfterAddingFile = (fileItem) => {
                    this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
                   }
                  this.form = this.fb.group({
                    libelle: ['', Validators.required],
                    type: ['0', Validators.required],
                    description: '',
                    joint: '',
                   });
              }

  ngOnInit() {
    this.fetchData();
  
  }
  
  public fileOverBase(e:any):void {
    this.hasBaseDropZoneOver = e;
  }
 
  public fileOverAnother(e:any):void {
    this.hasAnotherDropZoneOver = e;
  }
  rotateArrow(section,action){
    let files : HTMLElement = document.getElementById(section) as HTMLElement;
    if(!files.classList.contains('down')){
      files.classList.add('down') ;
    }else{
      files.classList.remove('down');
    }
  }

  removeFile(item){
    this.removing = true;
    item.remove();
    setTimeout(()=>{    
      this.removing = false;
    }, 500); 
    this.changeDetectorRef.detectChanges();
  }

  fetchData(){
    this.adminService.getFeedbacks()
        .subscribe(res => {
        this.MyDataSource = new MatTableDataSource();
        this.MyDataSource.data = res;
        this.MyDataSource.paginator = this.paginator;
        this.MyDataSource.sort = this.sort;
  }, err => {
    console.log(err);
  });

  }

  onSubmit(){
    if(this.form.valid){
      this.submited = true;
        if (this.uploader.queue.length > 0) {
              for(var i =0;i<this.uploader.queue.length;i++){
              this.uploader.queue[i].upload()
              this.uploader.onCompleteItem = (item: any, response: any , status: any, headers: any) => {
                if(response)
                this.docList.push(JSON.parse(response).uploadname);
                else{
                  this.snackBar.open('Une erreur c\'est produit verifier votre connection', 'X', {
                     duration: 6000,
                     verticalPosition: 'top',
                     panelClass:['custom-snack-error'],
                   });
                }
              }
            }
          }

      let feedback = this.form.value;
      if(this.uploader.queue.length > 0){
      this.uploader.onCompleteAll = () => {
        feedback.files = this.docList;
        this.adminService.addFeedback(feedback).subscribe(data => {
          if (data.success) {
            this.fetchData();
            this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
            this.closeModal.nativeElement.click() //<-- here
            this.form.reset();
          } else {
            this.snackBar.open(data.msg, 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack-error'],
           });
          }
          this.submited = false;
         });
      }
    }else{
      this.adminService.addFeedback(feedback).subscribe(data => {
        if (data.success) {    
          this.fetchData();
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
          this.closeModal.nativeElement.click() //<-- here
          this.form.reset();
        } else {
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
        }
        this.submited = false;
       });
    }
    }else{
      this.snackBar.open('Verifier les champs', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
      this.submited = false;
    }
}

    applyFilter(filterValue: string) {
      this.MyDataSource.filter = filterValue.trim().toLowerCase();
    }
  
}
