import { Component, OnInit,Input} from '@angular/core';
import {EmployeeService} from '../../../services/employee/employee.service';
import {ManagementService} from '../../../services/management/management.service';
import {Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../../../environments/environment';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-create-timesheet',
  templateUrl: './create-timesheet.component.html',
  styleUrls: ['./create-timesheet.component.scss']
})
export class CreateTimesheetComponent implements OnInit {
  @Input() selectedMonth : any;
  @Input() userId : any;
  @Input() selectedYear : any;
  user : any;
  month : any;
  id : any;
  data : any;
  newData : any;
  result =[];
  total = [];
  createMonth: any;
  months = ['Janv','Fvr','Mars','Avr','Mai','Juin','Juill','Aot','Sept','Oct','Nov','Dc'];
  defaults = ['Administration','CNSS','Formation','AID','Maladie','Sans affectation','Archivage','Gestion du cabinet','Documentation','Procdures et organisations','Congs Pays'];
  year = new Date().getFullYear();
  awsUrl = environment.awsUrl;
  timesheet : any;
  constructor(
    private emplyeeService :EmployeeService,
    private mService : ManagementService,
    private activatedRoute: ActivatedRoute,
    private router : Router,
    private snackBar: MatSnackBar,
  ) { }

  ngOnInit() {    
    this.activatedRoute.queryParams.subscribe(params => {
      if(params['param']){
      this.createMonth = params['param'];
      this.month = params['param'];
      this.setMonths();
      this.total = this.initArray();
      this.setNewTimesheet();
      this.getUser();
      }else{
        if(params['id']){
          this.id = params['id'];
          this.mService.getTimesheet(this.id).subscribe(res => {
            this.timesheet = res ;
            this.month = this.timesheet.month;
            this.year = this.timesheet.year;
           this.newData = new Array();
           this.data = new Array();
           this.setMonths();
           this.timesheet.data.forEach((element,index) => {
             this.newData.push({
               title : element.title,
               value : this.initArray(),
             })
             this.data.push({
              title : element.title,
              value : this.initArray(),
            })
            element.value.forEach((val,j) => {
              this.data[index].value[j] += Number(val);
              this.newData[index].value[j] += Number(val);

            });

           });
            this.initTotal();
            this.getUser();
          }, err => {
              console.log(err);
          });
        }else{
            this.id = this.userId
            this.mService.getTimesheetsByMonth(this.selectedYear,this.id,this.selectedMonth).subscribe(res => {
              this.timesheet = res[0] ;
              this.month = this.timesheet.month;
              this.year = this.timesheet.year;
              this.data = this.timesheet.data;
             // this.newData = this.timesheet.data;
             this.newData = [];
             this.data.forEach(element => {
               this.newData.push(element);
             });
              this.setMonths();
              this.initTotal();
              this.getUser();
            }, err => {
                console.log(err);
            });
          
        }
      }
      
    })
 
  }

  deleteRow(j){
    this.data.splice(j,1);
    this.initTotal();
  }

  initTotal(){
    for(var i =0 ; i < this.result.length ; i ++){
    this.total[i] = 0;
      for(var j = 0; j < this.data.length ; j++){
      
      this.total[i] += Number(this.newData[j].value[i]);
    }
  }
  }

  getUser(){
    if(!this.id){
    this.emplyeeService.getOneUser(this.emplyeeService.getIdfromToken())
    .subscribe(res => {
      this.user = res ;
      console.log(this.user);
    }, err => {
      console.log(err);
    });
  }else{
    if(this.timesheet){
    this.emplyeeService.getOneUser(this.timesheet.user)
    .subscribe(res => {
      this.user = res ;
      console.log(this.user);
    }, err => {
      console.log(err);
    });
  }

}
  }

  setNewTimesheet(){
    this.data = [];
    this.newData = [];
    this.defaults.forEach(def => {
      this.data.push({
        title : def,
        value : this.initArray(),
      });
      this.newData.push({
        title : def,
        value : this.initArray(),
      })

    });

    
    console.log(this.data);
  }

  getTotal(i){
    this.total[i] = 0;
    for(var j = 0; j < this.data.length ; j++){
      //this.total[i] = Number(this.total[i]);
      console.log(j);
      console.log(Number(this.newData[j].value[i]));

      this.total[i] += Number(this.newData[j].value[i]);
      
    }
  }


  addRow(){
    let object = {
      title: 'Nouvelle ligne',
      edit :true,
      value : this.initArray(),
    };
    let object1 = {
      title: 'Nouvelle ligne',
      edit :true,
      value : this.initArray(),
    };
    this.data.splice(this.data.length,0,object) ;
    this.newData.splice(this.data.length,0,object1) ;
    console.log(this.data);
    
  }

  noEnter(event){
    console.log(event);
    if(event.keyCode == 13){
    event.preventDefault();
    return ;
    }
  }

  setTitle(i,j){
    let element = document.getElementById(j+'-'+i);
    if(element.textContent == ''){
      element.textContent = 'Nouvelle ligne' ;      
    }
    console.log(element.textContent);
    this.newData[j].title = element.textContent; 
    //this.getTotal(i);
    console.log(this.newData[j]);
    element.blur();
  }

  saveTimesheet(){
    if(!this.id){
    let timesheet = {
      user : this.user._id,
      month : this.month,
      year: this.year,
      data : this.newData,
    }
    this.mService.createTimesheet(timesheet).subscribe(data => {
      if (data.success) {
        this.router.navigate(['/mytimesheets']);
        this.snackBar.open(data.msg , 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
        } else {
          console.log(data);
          this.snackBar.open(data.msg , 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
        }
     });
    }else{
      if(!this.timesheet.updates)
      this.timesheet.updates = [];
      this.timesheet.updates.push(new Date());
      this.timesheet.data = this.newData ;
      this.mService.updateTimesheet(this.id,this.timesheet).subscribe(data => {
        if (data.success) {
          this.router.navigate(['/mytimesheets']);
        this.snackBar.open(data.msg , 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
        } else {
          console.log(data);
          this.snackBar.open(data.msg , 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
        }
       });
    }
  }

  getData(i,j){
   // console.log(i+''+j);
    let element = document.getElementById(j+'-'+i);
  
    
    if(Number.isNaN(Number(element.textContent)) || element.textContent == '' || Number(element.textContent) < 0 || Number(element.textContent) > 8){
      element.textContent = '0' ;      
    }
    this.newData[j].value[i] = Number(element.textContent); 
    this.getTotal(i);

  }

  checkWeekend(i){
    let d = new Date();
    d.setDate(i);
    d.setMonth(this.month);
    return (d.getDay() == 6 || d.getDay() == 0);
  }

  initArray(){
    let x = [];
    this.result.forEach(element => {
      x.push(element);
    });

    return x ;
  }

  setMonths(){
    let  x = Number(this.month) + 1;
    console.log(x);
    this.result = [] ;
     if([1,3,5,7,8,10,12].includes(x)){
       for(let i = 1 ; i < 32 ; i++){
         this.result.push(0);
       }
     }else{
       if(x == 2){
         let x ;
         if ((Number(this.year)%4==0) && ((Number(this.year)%100!=0) || (Number(this.year)%400==0)))
          x = 30 ;
         else
         x = 29 ;
       for(let i = 1 ; i < x ; i++){
         this.result.push(0);
       }
     }else{
       for(let i = 1 ; i < 31 ; i++){
         this.result.push(0);
       }
     }
   }
     
   }

}
