import { Component, OnInit, ViewChild} from '@angular/core';
import { environment } from '../../../environments/environment';
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import {  FileUploader } from 'ng2-file-upload';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import {emailValidation} from '../../services/validations';
import {ConfigurationService} from '../../services/configuration/configuration.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';

@Component({
  selector: 'app-admin-config',
  templateUrl: './admin-config.component.html',
  styleUrls: ['./admin-config.component.scss']
})
export class AdminConfigComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  displayedColumns: string[] = ['name','mat_fis','adress','image','email','action'];
  MyDataSource: any;
  baseUrl = environment.baseUrl ;
  uploadUri = environment.uri;
  awsUrl = environment.awsUrl;  
  form: FormGroup ;
  postesForm: FormGroup ;
  uploader: FileUploader = new FileUploader({url: this.uploadUri});
  public image: string ;
  depart : any;
  selectedDepart : any;
  departs = [];
  departId : any;
  service : any;
  configId : any;
  id : any;
  public filePreviewPath: SafeUrl;

  constructor(
    private cService: ConfigurationService,
    private fb: FormBuilder,
    private snackBar: MatSnackBar,
    private sanitizer: DomSanitizer,
  ) { 

    this.uploader.onBeforeUploadItem = (item) => {
      item.withCredentials = false;
    }
    this.uploader.onAfterAddingFile = (fileItem) => {
      this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
    }

    this.form = this.fb.group({
      name: ['', Validators.required],
      email: new FormControl('', [
        Validators.required,
        emailValidation,
      ]),
      mat_fis : ['',Validators.required],
      adress: ['', Validators.required],
      image : '',
      postes : [],
      poste : '',

    });
    this.postesForm = this.fb.group({
      postes : [],
      poste : '',
    });

  }


  ngOnInit() {
    this.getConfig();
    this.fetchDeparts();
  }

   
  fetchDeparts(){
    this.cService.getAllDeparts()
    .subscribe(res => {
    this.departs = res ;
    console.log(this.departs);
    }, err => {
      console.log(err);
    });
  }

  submitDepart(){
    let form = {
      name : this.depart,
    }
    if(this.depart != ''){
    if(this.departId) {
      this.cService.updateDepart(this.departId, this.selectedDepart).subscribe(data => {
       if (data.success) {
         this.fetchDeparts();
         this.depart = '';
         this.snackBar.open('Mise  jours avec succs', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
           });
         } else {
           this.snackBar.open('Une erreur s\'est produite', 'X', {
            duration: 6000,
            verticalPosition: 'top',
            panelClass:['custom-snack-error'],
           });
       }
      });
     } else {
       this.cService.createDepart(form).subscribe(data => {
         if (data.success) {
           this.fetchDeparts();
           this.depart = '';
           this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
       } else {
         this.snackBar.open(data.msg, 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
         }
        });
     }
  }
}


ajouterPoste(){
  if(this.form.controls.poste.value != ''){
  this.form.controls.postes.value.push(this.form.controls.poste.value);
  this.form.controls.poste.patchValue('');
}
}

getConfig(){
  this.cService.getConfig()
  .subscribe(res => {
    console.log(res);
    if(res.length > 0){
      this.postesForm.patchValue({
        postes: res[0].postes
      });
      this.id = res[0]._id
      this.MyDataSource = new MatTableDataSource();
      this.MyDataSource.data = res[0].structure;
      this.MyDataSource.paginator = this.paginator;
      this.MyDataSource.sort = this.sort;
}
  }, err => {
    console.log(err);
  });
}

select(data){
  
  this.form.patchValue(data);
  this.configId = data._id;
}



afterLoad(){
  if(this.configId) {
    this.cService.updateConfig(this.id, this.configId, this.form.value).subscribe(data => {
     if (data.success) {
       this.getConfig();
       this.snackBar.open('Mise  jours avec succs', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
       } else {
         this.snackBar.open('Une erreur s\'est produite', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
     }
    });
   } else {
     console.log(this.form.value);
     this.cService.addConfig(this.id,this.form.value).subscribe(data => {
       if (data.success) {
        this.getConfig();
         this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
       } else {
         this.snackBar.open(data.msg, 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
       }
      });
   }
}


setDepart(depart){
  this.departId = depart._id;
  this.depart = depart.name;
  this.selectedDepart = depart ;
}

ajouterService(){
  if(this.service != ''){
this.selectedDepart.services.push(this.service);


}
}
onSubmit(){
  console.log(this.form.value);
  if (this.uploader.queue.length > 0) {
    this.uploader.queue[this.uploader.queue.length - 1].upload()  
    this.uploader.onCompleteItem = (item: any, response: any , status: any, headers: any) => {
      this.form.controls.image.setValue(JSON.parse(response).uploadname);  
      this.afterLoad();
    }
  }else{
    this.form.removeControl('image');
    this.afterLoad();
  }
}

applyFilter(filterValue: string) {
  this.MyDataSource.filter = filterValue.trim().toLowerCase();
}


}
