import { Component, OnInit, Inject, OnDestroy, HostListener, ViewEncapsulation, Renderer2 } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import {AuthService} from '../../services/auth/auth.service';
import {EmployeeService} from '../../services/employee/employee.service';
import {ManagementService} from '../../services/management/management.service';
import {ConfigurationService} from '../../services/configuration/configuration.service';
import * as Chartist from 'chartist';
import ChartistTooltip from 'chartist-plugin-tooltips-updated';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class DashboardComponent implements OnInit, OnDestroy {
  permissions: any[];
  users = [] ;
  manager = ['accounting','consulting','legal','audit'];
  groupes : any ; 
  services = []; 
  years = [] ;
  months : any[];
 departs = [];
  selectedMonth : any;
  selectedYear : any ;
  selectedDepart : any ;

  constructor( 
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    public authservice: AuthService,
    private employeService : EmployeeService,
    private mService : ManagementService, 
    private cService : ConfigurationService,
    ) { }

    ngOnDestroy(): void {
      if(this.authservice.introJS){
      this.renderer.removeClass(this.document.body, 'embedded-body0');
    }
  
  }
  
    ngOnInit() {
      if(this.authservice.introjs)
      this.renderer.addClass(this.document.body, 'embedded-body0');

    this.cService.getAllDeparts()
    .subscribe(res => {
      this.departs = res ;
      this.getUsers();
      this.getYearsFromServices();
      this.fetchGroupes();
  }, err => {
    console.log(err);
  }); 
  }


  setFilter(){
     this.months = ['Tout','Janv','Fvr','Mars','Avr','Mai','Juin','Juill','Aot','Sept','Oct','Nov','Dc'];
     this.selectedMonth = 0;
     this.selectedDepart = 'all';
    this.setChart();
      } 


      setChart(){
     
          let labels = [] ;
          let cout = [];
          let revenu = [] ;
          let depart : any ;
        if(this.selectedMonth == 0){
          labels = ['Janv','Fvr','Mars','Avr','Mai','Juin','Juill','Aot','Sept','Oct','Nov','Dc'];
          for(let i = 0 ; i < 12 ; i++){
            cout.push(0);
            revenu.push(0);
          }   
        }
          else
          if(['1','3','5','7','8','10','12'].includes(this.selectedMonth)){
            for(let i = 1 ; i < 32 ; i++){
              labels.push(i);
              cout.push(0);
              revenu.push(0);
            }
          }else{
            if(this.selectedMonth == 2){
              let x ;
              if ((Number(this.selectedYear)%4==0) && ((Number(this.selectedYear)%100!=0) || (Number(this.selectedYear)%400==0)))
               x = 30 ;
              else
              x = 29 ;
            for(let i = 1 ; i < x ; i++){
              labels.push(i);
              cout.push(0);
              revenu.push(0);
            }
          }else{
            for(let i = 1 ; i < 31 ; i++){
              labels.push(i);
              cout.push(0);
              revenu.push(0);
            }
          }
          }
          
      
        this.services.forEach(service => {
          if(service.createdAt.substring(0,4) == this.selectedYear){
            let newCost = 0;
            if(service.finish_date == 'En cours'){
              service.tasks.forEach(element => {
                newCost += Number(element.cost.replace(/\s/g, '')) ;
              });
              
            }else{
              newCost = Number(service.coutFinal.replace(/\s/g, '')) ;
            }
            if(this.selectedDepart != 'all'){
          if(service.depart == this.selectedDepart){
              if(this.selectedMonth == 0){
                cout[Number(service.createdAt.substring(5,7))-1] += newCost;     
                revenu[Number(service.createdAt.substring(5,7))-1] += Number(service.montant.replace(/\s/g, '')) ;
              }else{
                if(Number(service.createdAt.substring(5,7)) == this.selectedMonth){
                  cout[Number(service.createdAt.substring(8,10))] += newCost;
                  revenu[Number(service.createdAt.substring(8,10))] += Number(service.montant.replace(/\s/g, '')) ;
                }
              }
          } 
        }else{
          if(this.selectedMonth == 0){
            cout[Number(service.createdAt.substring(5,7))-1] += newCost ;
            revenu[Number(service.createdAt.substring(5,7))-1] += Number(service.montant.replace(/\s/g, '')) ;
          }else{
            if(Number(service.createdAt.substring(5,7)) == this.selectedMonth){
              cout[Number(service.createdAt.substring(8,10))] += newCost ;
              revenu[Number(service.createdAt.substring(8,10))] += Number(service.montant.replace(/\s/g, '')) ;
            }

        }
          }
        }
        });
          console.log(revenu);
           console.log(cout);
      
      var datawebsiteViewsChart =  {
        labels: labels,
        series: [
          cout,
         revenu
        ]
      };
    
    
      var optionswebsiteViewsChart = {
          chartPadding: { top: 5, right: 0, bottom: 5, left: 15},
          plugins: [ChartistTooltip()],
        
    
      
      };
    
      ///responsive chart
      var responsiveOptions: any[] = [
        ['screen and (max-width: 640px)', {
          
          seriesBarDistance: 5,
          axisX: {
            labelInterpolationFnc: function (value) {
              return value[0];
            }
          }
        }]
      ];
    
      /// css prob for labels
    
    
        var chart = new Chartist.Bar('#chart', datawebsiteViewsChart, optionswebsiteViewsChart, responsiveOptions);
    
        chart.on('draw', function(event) {
          if(event.type === 'label' && event.axis.units.pos === 'x') {
              event.element.attr({
                x: event.x + event.width / 2 - 10,
                'text-anchor': 'middle'
              });
           
          }
        });
        /// call animation fn 
        this.startAnimationForBarChart(chart);
    
  
    }

    startAnimationForBarChart(chart){
      let seq2: any, delays2: any, durations2: any;
    
      seq2 = 0;
      delays2 = 80;
      durations2 = 500;
      chart.on('draw', function(data) {
        if(data.type === 'bar'){
            seq2++;
            data.element.animate({
              opacity: {
                begin: seq2 * delays2,
                dur: durations2,
                from: 0,
                to: 1,
                easing: 'ease'
              }
            });
        }
      });
    
      seq2 = 0;
    };

  getUsers(){
    this.employeService.getUsers()
    .subscribe(res => {
      this.users = res ;
    }, err => {
      console.log(err);
    });
  }

  fetchGroupes(){
    this.authservice.getAllGroupes()
    .subscribe(res => {
      this.groupes = res ;
      console.log(this.groupes);
    }, err => {
      console.log(err);
    });
  }

  getYearsFromServices(){
    this.mService.getYearsFromServices()
    .subscribe(res => {
      res.forEach(element => {
        if(!this.years.includes(new Date(element).getFullYear()))
        this.years.push(new Date(element).getFullYear());
      });
      this.selectedYear = this.years[this.years.length - 1];
      this.getServicesByYear();
    }, err => {
      console.log(err);
    });
  }

  getServicesByYear(){
    this.mService.getServicesByYear(this.selectedYear)
    .subscribe(res => {
    this.services = res ; 
    this.setFilter();  
    }, err => {
      console.log(err);
    });
  }


  getDepartUsers(perm){
    //(.match(new RegExp("str", "g")) || []).length)
    let countUsers = 0 ;
    this.users.forEach(user => {
        if(user.departement == perm)
        countUsers++;
    });
  return countUsers;
  
}

getDepartServices(perm){
  let amount = 0 ;
  let serviceCount = 0;
  let cost = 0 ;
if(this.services){
  this.services.forEach(service => {
    if(service.depart == perm){
        amount += Number(service.montant.replace(/\s/g, '')) ;
        if(service.finish_date != 'En cours'){
          cost += Number(service.coutFinal.replace(/\s/g, '')) ;
          }
          else{
          serviceCount++;
          service.tasks.forEach(element => {
            cost += Number(element.cost.replace(/\s/g, '')) ;
          
          });  
        }
    }
   
  });
}
let result = {
  "amount" : Number(amount.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '),
  "cost" : Number(cost.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '),
  "services" : serviceCount
}
return result;
}

checkHidden(depart){
  let element :HTMLElement = document.getElementById(depart) as HTMLElement;
  
return element.classList.contains('col-md-12');
}
showDepart(depart){
  let element :HTMLElement = document.getElementById(depart) as HTMLElement;
  if(element.classList.contains('col-md-12')){
  element.classList.remove('col-md-12');
  element.classList.add('col-md-3');
  }else{
    element.classList.remove('col-md-3');
    element.classList.add('col-md-12');
  }
  
}

}
