import { Component, OnInit ,Input,ViewChild} from '@angular/core';
import {ManagementService} from '../../../services/management/management.service';
import {EmployeeService} from '../../../services/employee/employee.service';
import {NotificationService} from '../../../services/notification/notification.service';
import {ConfigurationService} from '../../../services/configuration/configuration.service'
import {AdminService} from '../../../services/admin/admin.service';
import {AuthService} from '../../../services/auth/auth.service';
import {FormGroup, FormBuilder, FormControl, Validators, FormArray, FormGroupDirective} from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import {Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-task-management',
  templateUrl: './task-management.component.html',
  styleUrls: ['./task-management.component.scss']
})
export class TaskManagementComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  formTask : FormGroup;
  @Input() toDepart : string;
  MyDataSource: any;
  tasks = [];
  deleteId : any;
  id : any ;
  displayedColumns: string[] = ['nom','rib','adresse','action'];
  constructor(
    private adminService: AdminService,
    private employeeService : EmployeeService,
    private cService: ConfigurationService,
    private fb: FormBuilder,
    private snackBar: MatSnackBar,
    private authservice: AuthService,
    private mService : ManagementService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private notificationService : NotificationService,
  ) {
    this.formTask = this.fb.group({ 
      depart: ['', Validators.required],
      task: ['', Validators.required],
      time: ['', Validators.required],
      repeat:false,
    });  
   }

  ngOnInit(): void {
    if(this.toDepart)
    this.formTask.get('depart').patchValue(this.toDepart);
    this.fetchTasks();
  }


  onSubmitTask(formDirective : FormGroupDirective){
    if(!this.id){
    this.mService.createTask(this.formTask.value).subscribe(data => {
      if (data.success) {
        this.formTask.reset();
        formDirective.resetForm();
        if(this.toDepart)
        this.formTask.get('depart').patchValue(this.toDepart);
        this.fetchTasks();
        this.snackBar.open(data.msg, 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack'],
           });
      } else {
        console.log(data);
        this.snackBar.open(data.msg, 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack-error'],
           });
      }
     });
    }else{
      this.mService.updateTask(this.id,this.formTask.value).subscribe(data => {
        if (data.success) {
          this.formTask.reset();
          formDirective.resetForm();
          this.id = '';
          if(this.toDepart)
          this.formTask.get('depart').patchValue(this.toDepart);
          this.fetchTasks();
          this.snackBar.open('Mise  jours avec succs', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
        } else {
          console.log(data);
          this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
        }
       });
    }
  }


  numericOnly(event): boolean {
    const patt =  /^\d*\.?\d*$/;
    return patt.test(event.key);
  }

  deleteTask(){
    let task = this.tasks.find(one => one._id == this.deleteId);
    task.active = false;
    this.mService.updateTask(this.deleteId,task).subscribe(data => {
      if (data.success) {
        this.deleteId = '';
        const element: HTMLElement = document.getElementById('hideModal') as HTMLElement;
        element.click();
        this.fetchTasks();
        this.snackBar.open('Tche supprime', 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack'],
           });
      } else {
        console.log(data);
        this.snackBar.open(data.msg, 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack-error'],
           });
      }
     });
  }
  
  applyFilter(filterValue){
    this.MyDataSource.filter = filterValue.trim().toLowerCase();
  }

  setTask(one){
    this.id = one._id ;
    this.formTask.patchValue(one);
  }
  
  fetchTasks(){
    this.mService.getTasks()
    .subscribe(res => {
      this.tasks = res.filter(task => task.departement == this.formTask.get('depart').value && task.active) ;
      this.MyDataSource = new MatTableDataSource(this.tasks);
      this.MyDataSource.paginator = this.paginator;
      this.MyDataSource.sort = this.sort;
    }, err => {
      console.log(err);
    });
  }
}
