import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import {AppeldoffreService} from '../../services/appeldoffre/appeldoffre.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { environment } from '../../../environments/environment';
import {AuthService} from '../../services/auth/auth.service';
import {NotificationService} from '../../services/notification/notification.service';
import {EmployeeService} from '../../services/employee/employee.service';
import {FormGroup, FormBuilder, Validators, FormControl} from '@angular/forms';

import * as decode from 'jwt-decode';
export interface linkModel{
  url? : string ;
  params? : string ;
  paramsValue?:string ;
}


@Component({
  selector: 'app-Appel-offre',
  templateUrl: './appel-offre.component.html',
  styleUrls: ['./appel-offre.component.scss'],
  encapsulation:ViewEncapsulation.None
})
export class AppelOffreComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  uri = environment.baseUrl;
  MyDataSource: any;
  displayedColumns: string[] = ['reference', 'titre', 'categorie', 'status', 'depot', 'action'];
public Appels : any[] ;
public appel : any ;
public clients : any ;
public groupes : any[] ;
public appelServices : Array<any> ;
public services : any ;
categories: any;
raisons: any;
filteredRaisons: any; 
reasonForm : FormGroup;
addReasonForm : FormGroup;
public oneAppel : any ;
public tasks : any ;
public deleteId : string;
public Submiting = false;
public rejectForm = true;
public notification : any ;
public notifLink : linkModel = {} ;

  constructor(public authservice: AuthService,
              private appeldoffreService: AppeldoffreService, 
              private emplyeeService : EmployeeService,
              private notificationService : NotificationService,
              private snackBar: MatSnackBar,
              private fb: FormBuilder,
              ) { 
                this.reasonForm = this.fb.group({
                  raison: ['', Validators.required]
                });
                this.addReasonForm = this.fb.group({
                  name: ['', Validators.required]
                });
                this.getCategories();
                this.fetchRaisons();
              }

  ngOnInit() {
    this.fetchData();
  }

  fetchRaisons(){
    this.appeldoffreService.getRaisons()
    .subscribe(res => {
     this.raisons = res ;
     this.filteredRaisons = this.raisons.slice();
    }, err => {
      console.log(err);
    });
  }

  togglRejectModal(data){
    this.oneAppel = data;
  }

  togglModal(id){
    this.deleteId = id;
  }
  delete(id){
    this.appeldoffreService.deleteAppel(id)
    .subscribe(res => {
        this.fetchData();
        let element :HTMLElement = document.getElementById('hideModal') as HTMLElement;
        element.click();
        this.snackBar.open('Appel d\'offre supprim', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
      }, (err) => {
        console.log(err);
      });
    }
    
    fetchData(){
      this.appeldoffreService.getAllAppels()
      .subscribe(res => {
        this.MyDataSource = new MatTableDataSource();
        res.forEach(appel => {
          appel.cat = '';
          this.categories.forEach(cat => {
            if(appel.category.includes(cat._id)){
              appel.cat = appel.cat + '| ' + cat.name;
            }
            
          });
        });
        this.MyDataSource.data = res;
        
        this.MyDataSource.paginator = this.paginator;
        this.MyDataSource.sort = this.sort;
        this.Appels = res ;
        console.log(this.Appels);
      }, err => {
        console.log(err);
      });
    }

    getCategories(){
      this.appeldoffreService.getCategories()
      .subscribe(res => {
       this.categories = res ;
       console.log(res);
      }, err => {
        console.log(err);
      });
    }

  applyFilter(filterValue: string) {
    this.MyDataSource.filter = filterValue.trim().toLowerCase();
  }
  
  showActions(id){
    let actions : HTMLElement = document.getElementById(id) as HTMLElement;
 if(actions.classList.contains('tasks')){
    actions.classList.remove('tasks');
 }  else{
    actions.classList.add('tasks');
    }
  }

  StringAsDate(dateStr: string) {
    return new Date(dateStr);
  }

  changerEtat(data, etat){
    if(this.authservice.permissions.includes('appelsUpdate')){
    this.Submiting = true;        
    let appeldoffre;
       appeldoffre = {
        etat : etat,
      };

      this.appeldoffreService.updateAppel(data._id,appeldoffre).subscribe(data => {
        if (data.success) {
          this.notifLink.url = '/editAppel';
          this.notifLink.params = 'id' ;
          this.notifLink.paramsValue = data._id ;
            this.notification = {
              'sender': this.emplyeeService.getIdfromToken(),
              'receiver': data.users,
              'title': 'Appele d\'offre',
              'image': this.emplyeeService.getImagefromToken(),
              'message': 'Etat d\'appel d\'offre est change',
              'link': this.notifLink,
            };

          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
          this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
            console.log(data2);
            if (data2.success) {                            
              this.fetchData();
              this.Submiting = false;
            }else {
              this.snackBar.open('Erreur notification Contacter xpr.com.tn!!!', 'X', {
                duration: 6000
              });
            }
           });
        } else {
          console.log(data);
          this.snackBar.open(data.msg, 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
        }
       });
      }
  }


  
  reject(data, etat){
    if(this.reasonForm.valid){
    this.Submiting = true;
    let appeldoffre;
       appeldoffre = {
        etat : etat,
        raison : this.reasonForm.get('raison').value
        ,
      };

      this.appeldoffreService.updateAppel(data._id,appeldoffre).subscribe(data => {
        if (data.success) {
          this.notifLink.url = '/editAppel';
          this.notifLink.params = 'id' ;
          this.notifLink.paramsValue = data._id ;
            this.notification = {
              'sender': this.emplyeeService.getIdfromToken(),
              'receiver': data.users,
              'title': 'Appele d\'offre',
              'image': this.emplyeeService.getImagefromToken(),
              'message': 'Etat d\'appel d\'offre est change',
              'link': this.notifLink,
            };

            this.snackBar.open(data.msg, 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack'],
           });
          this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
            console.log(data2);
            if (data2.success) {                            
              this.fetchData();
              let element :HTMLElement = document.getElementById('hideReasonModal') as HTMLElement;
              element.click();
              this.Submiting = false;
            }else {
              this.snackBar.open('Erreur notification Contacter xpr.com.tn!!!', 'X', {
                duration: 6000
              });
            }
           });
        } else {
          console.log(data);
          this.snackBar.open(data.msg, 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
        }
       });

  }else {
    this.snackBar.open('Selectionner au moin une raison', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
  }
 }

 addRaison(){
  if(this.addReasonForm.valid){
    this.Submiting = true;
    this.appeldoffreService.addRaison(this.addReasonForm.value).subscribe(data => {
      console.log(data);
      if (data.success) {
        this.addReasonForm.reset();
        let element :HTMLElement = document.getElementById('returnReasonModal') as HTMLElement;
        element.click();
        this.fetchRaisons();
        this.Submiting = false;
        this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
       } else {
         this.snackBar.open(data.msg, 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
      }
     });
  }else{
    this.snackBar.open('Verifier les champs', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
  }
}


}
