import { Component, OnInit , ViewEncapsulation } from '@angular/core';
import {formatDate} from '@angular/common';
import { FileUploader } from 'ng2-file-upload';
import { environment } from '../../../../environments/environment';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import {ManagementService} from '../../../services/management/management.service';
import {AdminService} from '../../../services/admin/admin.service';
import {EmployeeService} from '../../../services/employee/employee.service';

import {Router, ActivatedRoute } from '@angular/router';
import {FormGroup, FormBuilder, Validators, FormControl, FormArray} from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import {saveAs} from 'file-saver';
import {NotificationService} from '../../../services/notification/notification.service';
import {AuthService} from '../../../services/auth/auth.service';


const uri = environment.uri;

const files = environment.baseUrl+'/uploads/documents/';

export interface modelDoc{
  docName?:string;
  docPath?:string;
}
export interface linkModel{
  url? : string ;
  params? : string ;
  paramsValue?:string ;
}

@Component({
  selector: 'app-tasks',
  templateUrl: './tasks.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./tasks.component.scss']
})


export class TasksComponent implements OnInit {
  public uploader: FileUploader = new FileUploader({url: uri, removeAfterUpload: true , allowedFileType: ['image', 'doc', 'pdf', 'xls', 'ppt']});
  public hasBaseDropZoneOver:boolean = false;
  public hasAnotherDropZoneOver:boolean = false;
  public filePreviewPath: SafeUrl;
  public service : any ;
  public tasks : any ;
  public users : any ;
  public id:any ;
  public getTime : any ;
  documents : Array<modelDoc> = [];
  public notification : any ;
  public receiverId : any = [] ;
  public notifLink : linkModel = {} ;
  client : any ; 
  userId : any ;
  showUp : any = false;
  form : FormGroup ;
  formService: FormGroup;
  formGetTime : FormGroup ;
  formDemande : FormGroup ;
  depart : any ; 
  selectedTask : any ; 
  navTask : any;
  myTasks = [];
  timsheetDates = [];
  url =  environment.baseUrl ;
 awsUrl = environment.awsUrl;
  docList = [] ;
  constructor(
    private sanitizer: DomSanitizer,
    private mService : ManagementService,
    private adminService : AdminService,
    private emplyeeService : EmployeeService,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private notificationService : NotificationService,
    private authservice : AuthService,
    private router: Router,
    private snackBar: MatSnackBar,
  ) {
    this.formService = this.fb.group({
      taskName: ['', Validators.required],
      
    });

    this.formGetTime = this.fb.group({
      timePassed : this.fb.array([])
      
    });

    this.formDemande = this.fb.group({
      fileName: new FormControl('',Validators.required),
      
    });


    this.form = this.fb.group({
      rapport: '',
      
    });
  
    this.uploader.onBeforeUploadItem = (item) => {
      item.withCredentials = false;
     
    }
    this.uploader.onAfterAddingFile = (fileItem) => {
      this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
    }
   }

  ngOnInit() {
   
    this.getOneTimeService();
    this.fetchTasks();
    
    
 
  }


  loadfiles(taskId){ 
    this.uploader.uploadAll();
    this.uploader.onCompleteItem = (item: any, response: any , status: any, headers: any) => {
      let document : modelDoc = {};
          document.docName = JSON.parse(response).originalname.substring(0, JSON.parse(response).originalname.indexOf('.')) ;
          document.docPath = JSON.parse(response).uploadname;

      this.service.tasks.forEach(task => {
          if(task._id == taskId){
            task.documents.push(document);
          }
      });
 
    }

    this.uploader.onCompleteAll = () => {
      console.log(this.service);
      this.mService.updateService(this.id,this.service).subscribe(data => {
        if (data.success) {
          this.getService();
          this.snackBar.open('upload russi', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
       
        } else {
          console.log(data);
          this.snackBar.open('Une erreur s\'est produite', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
        }
       });
    }
  }

  public fileOverBase(e:any):void {
    this.hasBaseDropZoneOver = e;
  }
 
  public fileOverAnother(e:any):void {
    this.hasAnotherDropZoneOver = e;
  }

  fetchTasks(){
    this.mService.getTasks()
    .subscribe(res => {
      this.tasks = res ;
    }, err => {
      console.log(err);
    });
  }

  getOneTimeService(){
    this.activatedRoute.queryParams.subscribe(params => {
      this.id = params['id'];
       this.depart = params['d'];
    if (this.id) {
      this.mService.getOneService(this.id)
      .subscribe(res => {
         this.service = res ;
         res.clientDocs.forEach(doc => {
           this.docList.push(doc) ;
           
         });
         this.navTask = res.tasks[0]._id ;
         this.myTasks = [];
         res.tasks.forEach(task => {
          if(!this.myTasks.find(one => one.task == task.task)){
            let x = task ;
            if(task.repeat){
            x.default = task._id;
            }
          this.myTasks.push(x);
          }
         });
         console.log(this.myTasks),
         this.userId = this.emplyeeService.getIdfromToken() ;
         /// client 
         this.adminService.getOneClient(this.service.client)
         .subscribe(res => {
           this.client = res ;
         }, err => {
           console.log(err);
         });
         /// users
         this.emplyeeService.getUsers()
         .subscribe(res => {
           let supervisor : any ;
           this.users = res ;
          //  this.service.tasks.forEach(task => {
          //    supervisor = this.users.find(user => user._id == task.supervisorId) ;
          //    task.supervisorName = supervisor.fname +" "+ supervisor.lname ;
             
          //  });
         }, err => {
           console.log(err);
         });
         console.log(this.service);

      }, err => {
         console.log(err);
     });
    }
    else{
      this.router.navigate(['/']);
    } 
  });
  }


  getService(){
    this.activatedRoute.queryParams.subscribe(params => {
      this.id = params['id'];
       this.depart = params['d'];
    if (this.id) {
      this.mService.getOneService(this.id)
      .subscribe(res => {
         this.service = res ;
         res.clientDocs.forEach(doc => {
           this.docList.push(doc) ;
           
         });

         console.log(this.myTasks),
         this.userId = this.emplyeeService.getIdfromToken() ;
         /// client 
         this.adminService.getOneClient(this.service.client)
         .subscribe(res => {
           this.client = res ;
         }, err => {
           console.log(err);
         });
         /// users
         this.emplyeeService.getUsers()
         .subscribe(res => {
           this.users = res ;
         }, err => {
           console.log(err);
         });
         console.log(this.service);

      }, err => {
         console.log(err);
     });
    }
    else{
      this.router.navigate(['/']);
    } 
  });
  }


  showActions(id){
    let actions : HTMLElement = document.getElementById(id) as HTMLElement;
    if(actions.className !="")
    actions.className = "";
    else
    actions.className= "actions";
  }

  showUpload(state){
    this.showUp = state ;
  }

  getMonth(task){
    let date = new Date(task.date_deb);

    return date.toLocaleString('fr', { month: 'long' });
  }


  deleteTask(file,taskId){
 let task  = this.service.tasks.find(task => task._id == taskId);
 task.documents = task.documents.filter(document => document._id != file) ;
   this.mService.updateService(this.id,this.service).subscribe(data => {
      if (data.success) {
        this.snackBar.open('Document Supprim', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
     
      } else {
        console.log(data);
        this.snackBar.open('Une erreur s\'est produite', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
      }
     });
  }


  checkHour(i){
    let hour = (<FormArray>this.formGetTime.get('timePassed')).at(i).get('time').value
    if(hour < 0 || hour == '' || hour == null){
      (<FormArray>this.formGetTime.get('timePassed')).at(i).get('time').patchValue(0);
    }
  }

  checkOtherTimesheets(task){
    let x = true;
    task.helpers.forEach(help => {
  //console.log(help.timePassed.length);
  if(help != "" &&  help.timePassed.length == 0){
  x = false;
  }
});
return x;
  }

  setTimesheet(task){
    if((this.userId == task.user && this.checkOtherTimesheets(task)) || this.userId != task.user){
    this.formGetTime.removeControl('timePassed');
    this.formGetTime.addControl('timePassed',this.fb.array([]));
    this.selectedTask = task ;
    let date_fin = new Date();
    date_fin.setHours(0,0,0,0);
    let date_deb = new Date(task.date_deb);
    date_deb.setHours(0,0,0,0);
    //let diff = Math.floor((Date.UTC(date_fin.getFullYear(), date_fin.getMonth(), date_fin.getDate()) - Date.UTC(date_deb.getFullYear(), date_deb.getMonth(), date_deb.getDate()) ) /(1000 * 60 * 60 * 24));
    while(date_deb <= date_fin){
      var a = new Date();
      a.setHours(0,0,0,0);
      a.setDate(date_deb.getDate());
      a.setMonth(date_deb.getMonth());
    (<FormArray>this.formGetTime.get('timePassed')).push(
      this.fb.group({
        time :0,
        date : a
      })
    );
   // this.timsheetDates.push(date_deb);
    date_deb.setDate(date_deb.getDate()+1);
  }
    this.getTime = true;
}else{
  this.snackBar.open('Vous devez attendre la confirmation des autres collaborateurs', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
}
  }



  checkHelpDone(helpers){
    let x = true;
    helpers.forEach(help => {
      if(help.user == this.userId){
        if(help.timePassed.length > 0)
        x = false;
      }
    });
    return x ;
  }


  confirmTask(task){

  //let hours = +this.formGetTime.controls.time.value.substring(0,2) * 60 + parseInt(this.formGetTime.controls.time.value.substring(3,6)) ;
   let hours = 0;
   this.formGetTime.get('timePassed').value.forEach(one => {
     hours += one.time;
   });
   if(hours > 0){
   this.service.tasks.forEach(el => {
      if(el._id == task._id){
        if(this.checkHelper(el.helpers)){
          let salaire = Number(this.getCollab(task.user).contrat.salairebrut.replace(/\s/g, ''));
          let cost =  ((salaire / 22) / 8) * (hours / 60)+Number(el.cost.replace(/\s/g, ''));
          el.helpers.forEach(help => {
            if(help.user == this.userId){
              help.timePassed = this.formGetTime.get('timePassed').value ;
            }
          });
          el.cost = Number(cost.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
        }else{
        el.status = "completed";
        el.timePassed = this.formGetTime.get('timePassed').value ;
        el.finish_date = formatDate(new Date(), 'dd/MM/yyyy HH:mm', 'fr');
        let salaire = Number(this.getCollab(task.user).contrat.salairebrut.replace(/\s/g, ''));
        let cost =  ((salaire / 22) / 8) * (hours / 60)+Number(el.cost.replace(/\s/g, ''));
        el.cost = Number(cost.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
      }
      console.log(el);
    }
   });
   this.mService.updateService(this.id,this.service).subscribe(data => {
      if (data.success) {
        /////////// create notif if sender != receiver
        this.getTime = false;
        if(this.service.clientConsult != this.emplyeeService.getIdfromToken()){
        this.receiverId.push(this.service.clientConsult) ;
        this.notifLink.url = '/tasks';
        this.notifLink.params = 'id' ;
        this.notifLink.paramsValue = this.service._id ;

            if(this.checkHelper(task.helpers)){
              this.receiverId = [];
              this.receiverId.push(task.user);
            }

            this.notification = {
              'sender': this.emplyeeService.getIdfromToken(),
              'receiver': this.receiverId,
              'title': 'Tche termine',
              'image': this.getCollab(this.userId).image,
              'message': this.getCollab(this.userId).fname+' a termin la tache '+this.tasks.find(one => one._id == task.task).task+' dans '+this.service.name,
              'link': this.notifLink
            };
          
            this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
              if (data2.success) {
                console.log(this.notification);
                console.log(data2);
              }
             });
            }
            /////////
        this.snackBar.open('Tche termine', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
     
      } else {
        console.log(data);
        this.snackBar.open('Une erreur s\'est produite', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
      }
     });
    }
  }

  sendTask(task){
    let confirm = 0 ;
    let cost = 0;
    var length = 0 ;
    this.service.tasks.forEach(el => {
        length++;
        if(el._id == task){
         el.clientSide = true ;
        }
        if(el.clientSide == true){
        confirm++ ;
        cost += Number(el.cost.replace(/\s/g, ''));
        }
        
    });
  
    console.log(confirm+'  task '+length) ;
    if(confirm == length){
    this.service.finish_date = formatDate(new Date(), 'dd/MM/yyyy HH:mm', 'fr');
    this.service.coutFinal = Number(cost.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
    }
    console.log(this.service);
    this.mService.updateService(this.id,this.service).subscribe(data => {
      if (data.success) {
        //notif client
        let notifLinkClt = {
          url : '/docs',
          params: 'id',
          paramsValue:this.id,
        };
        this.notification = {
          'sender': this.authservice.getIdfromToken(),
          'receiver': this.service.client,
          'title': 'Service: '+this.service.name,
          'message':'Des documents sont arriv.',
          'link': notifLinkClt,
        };
          this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
            if (data2.success) {
              console.log(this.notification);
              console.log(data2);
            }
          });
        this.getService();
        this.snackBar.open('Les documents ont t envoys au client', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
      } else {
        console.log(data);
        this.snackBar.open('Une erreur s\'est produite', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
      }
     });
  }

  cancelTask(task){
    let user  :any ;
    if(this.form.controls.rapport.value != ""){
      this.service.tasks.forEach(el => {
            if(el._id == task._id){
              el.status = "En cours";
              el.finish_date = "En cours";
              el.rapport = this.form.controls.rapport.value;
              user = el.user ;
            }
      });
      console.log(this.service);
    this.mService.updateService(this.id,this.service).subscribe(data => {
      if (data.success) {
        console.log(data);
        this.getService();
        /////////// create notif if sender != receiver
        if(this.service.clientConsult != user){
        this.receiverId.push(user) ;
        this.notifLink.url = '/tasks';
        this.notifLink.params = 'id' ;
        this.notifLink.paramsValue = this.service._id ;
            this.notification = {
              'sender': this.emplyeeService.getIdfromToken(),
              'receiver': this.receiverId,
              'title': 'Tche Rejet',
              'image': this.getCollab(this.userId).image,
              'message': 'La tache '+task.task+' a t rejet ',
              'link': this.notifLink,
            };
            this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
              if (data2.success) {
                console.log(this.notification);
                console.log(data2);
              }
             });
            }
            ///////////
            if(this.service.clientConsult != user){
        this.snackBar.open('Rapport Envoy', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
      }else{
        this.snackBar.open('Note Enregistre', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
      }
     
      } else {
        console.log(data);
        this.snackBar.open('Une erreur s\'est produite', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
      }
     });
    }
    
   console.log(task);
  }

  getCollab(userId){
    if(this.users){
    let user = this.users.find(user => user._id == userId);
    return user ;
    }
  }

  checkHelper(helpers){
    return helpers.findIndex(one => one.user == this.userId) != -1 ;
  }

  downloadTask(file){
    var filename = file.docPath;
    this.mService.downloadDoc(filename)
    .subscribe(
      data => saveAs(data,file.docName+'.'+filename.split('.').pop()),
      error => console.error(error)
    );

    console.log(files) ;
   }

   addToList(){
     if(this.formDemande.controls.fileName.valid 
      && this.docList.some(clientDoc => this.formDemande.controls.fileName.value != clientDoc.docType) ){
    this.docList.push({
     "docType" : this.formDemande.controls.fileName.value,
     "docs" : [],
    });
    this.formDemande.reset();
    this.formDemande.markAsUntouched();
     }
   }

   editDocList(serviceName){
     if(serviceName != ''){
          this.docList.forEach(doc => {
            if(!this.service.clientDocs.some(clientDoc => doc.docType == clientDoc.docType)){
            this.service.clientDocs.push(doc) ;
            }
          });
          
          this.mService.updateService(this.id,this.service).subscribe(data => {
            if (data.success) {
              //notif client
                let notifLinkClt = {
                  url : '/docs',
                  params: 'id',
                  paramsValue:this.id,
                };
                this.notification = {
                  'sender': this.authservice.getIdfromToken(),
                  'receiver': this.service.client,
                  'title': 'Service: '+this.service.name,
                  'message':'La liste des fichiers  t mise  jours.',
                  'link': notifLinkClt,
                };
                  this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
                    if (data2.success) {
                      console.log(this.notification);
                      console.log(data2);
                    }
                  });
              this.formDemande.reset();
              this.formDemande.markAsUntouched();
              this.snackBar.open('Liste Enregistre', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
            }else{
              console.log(data);
              this.snackBar.open('Un erreur s\'est produite', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
            }
          }); 
     }
   }



   stringAsDate(dateStr: string) {
    return new Date(dateStr);
  }

  rotateArrow(section,action){
    let files : HTMLElement = document.getElementById(section) as HTMLElement;
    if(!files.classList.contains('down')){
      files.classList.add('down') ;
    }else{
      files.classList.remove('down');
    }
    this.showActions(action);

  }

  getTimePassed(time){
    let hours = time / 60 ;
    let min = time % 60 ;
    if(hours > 0 && min > 0)
    return hours + 'heures : '+min +' minutes';
    else
    if(min > 0)
    return min+' minutes';
    else
    return hours+' heures' ;
  }
}
