import { Component, OnInit,Input } from '@angular/core';
import { environment } from '../../../../environments/environment';
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import {ManagementService} from '../../../services/management/management.service';
import { MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-model-task',
  templateUrl: './model-task.component.html',
  styleUrls: ['./model-task.component.scss']
})
export class ModelTaskComponent implements OnInit {
  form: FormGroup ;
  @Input() depart: string;

  constructor(
    private fb: FormBuilder,
    private mService : ManagementService, 
    private snackBar : MatSnackBar      
  ) { 
    this.form = this.fb.group({ 
      depart: [this.depart, Validators.required],
      task: ['', Validators.required],
      time: ['', Validators.required],
      repeat:false,
    });       
  }

  ngOnInit() {
  }


  onSubmit(){
    this.form.controls.depart.patchValue(this.depart);
    console.log(this.form.value);
    this.mService.createTask(this.form.value).subscribe(data => {
      if (data.success) {
        this.form.reset();
        this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
      } else {
        console.log(data);
        this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
      }
     });
  }
}
