import { Component, OnInit, ViewChild } from '@angular/core';
import {Router, ActivatedRoute } from '@angular/router';
import {AuthService} from '../../services/auth/auth.service';
import { ServicesComponent } from '../services-management/services/services.component';
import { optionsToString } from 'rrule/dist/esm/src/optionstostring';
import { CreateServiceComponent } from '../services-management/create-service/create-service.component';
import { ModeleServiceComponent } from '../services-management/modele-service/modele-service.component';
import { MonthCalendarComponent } from '../month-calendar/month-calendar.component';
@Component({
  selector: 'app-service',
  templateUrl: './service.component.html',
  styleUrls: ['./service.component.scss']
})
export class ServiceComponent implements OnInit {
public depart: any ;
@ViewChild(ServicesComponent) private servicesComponent: ServicesComponent;
@ViewChild(CreateServiceComponent) private createService: CreateServiceComponent;
@ViewChild(MonthCalendarComponent) private calendar: ModeleServiceComponent;
@ViewChild(ModeleServiceComponent) private modelSerivce: ModeleServiceComponent;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    public authservice :AuthService,
  ) {
  }

  ngOnInit() {
    // this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    
    
    this.activatedRoute.queryParams.subscribe(params => {
      this.depart = params['id'];
      document.getElementById('all_services').click();
      console.log(this.depart);
    });

  }


  
  refrechAll(number){
    if(this.servicesComponent && number == 1)
    this.servicesComponent.fetchServices();
    else
    if(this.modelSerivce && this.servicesComponent && number == 2){
    this.createService.getModeleServices();
    this.createService.fetchTasks();
    this.servicesComponent.fetchServices();
    }else
    if(this.modelSerivce && number == 4){
      this.modelSerivce.fetchTasks();
    }
    else
    if(this.calendar && number == 3)
    this.calendar.ngOnInit();
  }

  

  goToServices(){
    let button = document.getElementById('all_services') ;
    button.click();
    
  }
  
  goToCreateService(){
    let button = document.getElementById('affectservice') ;
    button.click();
  }



}
