updateList(id: string, serviceId: number, property: string, event: any) {
    const input = <HTMLInputElement>event.srcElement;
    const editField =  Number(input.value.replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
  if (Number(input.value.replace(/\s/g, ''))) {
    this.services[serviceId][property] = editField;
    this.mService.updateMontant(id, editField).subscribe(data => {
        if (data.success) {
      this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
    } else {
        this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
    }
       });
      } else {
        this.snackBar.open('Veuillez saisir un montant valide', 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
      }
}

