getUnpaidAmount(service){
  let sum = 0 ;
  if(this.bills){
let bills = this.bills.filter(bill => bill.service == service._id);
bills.forEach(bill => {
  sum += Number(bill.amount.replace(/\s/g, ''));
});
  }
  let x = false ;
  if(sum < Number(service.montant.replace(/\s/g, ''))){
    x = true; 
  }
 let result = {
    amount : Number(sum.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '),
    check : x,
  }
return result;
}
