getTasks() : void {
  if(this.formService){
    if(this.formService.controls.compta){
      this.formService.removeControl('compta');
    }
   ;
   
    this.serviceTasks = [];
    this.formService.addControl('compta',this.fb.array([]));
    this.formService.controls.compta.reset();
    this.formService.controls.sName.value.tasks.forEach(element => {
      (<FormArray>this.formService.controls.compta).push(
        this.fb.group({
          task : [element,Validators.required],
          depart : this.tasks.filter(i => i._id == element)[0].departement,
          user:['',Validators.required],
          prio : '',
          status:'En cours',
          finish_date:'En cours'
          }) 
      );
        
     
       this.serviceTasks.push(this.tasks.filter(i => i._id == element)[0]);
    
        
      
     
        
    });
    this.departs.forEach(depart => {
      if(this.checkDepartement(depart)){
        this.formService.addControl('supervisor'+depart, new FormControl('', Validators.required));
      }
    });
    
    console.log(this.formService.value);
   console.log(this.serviceTasks);
   
}

}

checkDepartement(depart){
 return this.serviceTasks.some((k) => { return k.departement == depart});
}


