avanceService(tasks){
  let completed  = [] ;
  let all = [] ;
  tasks.forEach(one => {
      if(one.status == 'completed')
      completed.push(one);
      all.push(one);
  });
  return ((completed.length/all.length)* 100).toFixed(2);
 
}

