checkDisp(user){
  
  let  result = {
    "perc" : 0,
    "completed" : 0,
     "progress" : 0
   }
  
  this.services.forEach(service => {
    service.tasks.forEach(depart=> {
      depart.toDo.forEach(task => {
        if(task.user == user){
          if(task.status != 'completed')
            result.progress++ ;
            else{
              if(!task.clientSide)
              result.completed++ ;
            }
        }
      });
    });
  });
  if(result.progress != 0)
  result.perc = Math.floor((result.completed / result.progress)*100) ;
  return result ;
}

