import { Component, OnInit } from '@angular/core';
import {ManagementService} from '../../../services/management/management.service';
import {EmployeeService} from '../../../services/employee/employee.service';
@Component({
  selector: 'app-mytimesheets',
  templateUrl: './mytimesheets.component.html',
  styleUrls: ['./mytimesheets.component.scss']
})
export class MytimesheetsComponent implements OnInit {
years = [];
selectedYear : any;
showInput: boolean = false;
allmonths = ['Janv','Fvr','Mars','Avr','Mai','Juin','Juill','Aot','Sept','Oct','Nov','Dc'];
months = [];
timesheets = [];
selectedMonth = new Date().getMonth();
userId : any;

  constructor(
    private mService : ManagementService,
    private emplyeeService :EmployeeService,
  ) { 
    this.userId = this.emplyeeService.getIdfromToken();
  }

  ngOnInit() {
    this.getYearsFromServices();
  }

  getYearsFromServices(){
    this.mService.getYearsFromServices()
    .subscribe(res => {
      res.forEach(element => {
        if(!this.years.includes(new Date(element).getFullYear()))
        this.years.push(new Date(element).getFullYear());
      });
      this.selectedYear = this.years[this.years.length - 1];
      this.getTimesheetsByYear();
    }, err => {
      console.log(err);
    });
  }

  getTimesheetsByYear(){
    this.mService.getTimesheetsByYear(this.selectedYear,this.userId)
    .subscribe(res => {
    this.timesheets = res ; 
    this.setMonths();

    console.log(this.timesheets);
    //this.setFilter();  
    }, err => {
      console.log(err);
    });
  }


  setMonths(){
    for(var i = 0; i <= this.selectedMonth ; i++){
      if(!this.timesheets.find(one => one.month == i))
      this.months.push(this.allmonths[i]);
    }
    console.log(this.months);
  }
}
