import { Component, OnInit, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';
import {Router, ActivatedRoute } from '@angular/router';
import {AdminService} from '../../services/admin/admin.service';
import {ManagementService} from '../../services/management/management.service';
import {EmployeeService} from '../../services/employee/employee.service';
import {ConfigurationService} from '../../services/configuration/configuration.service';
import {AuthService} from '../../services/auth/auth.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import {environment} from '../../../environments/environment';
import * as $ from 'jquery';
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { MatDatepicker } from '@angular/material/datepicker';
import {NotificationService} from '../../services/notification/notification.service';
import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';

import { isThisMonth } from 'date-fns';

@Component({
  selector: 'app-bill',
  templateUrl: './bill.component.html',
  styleUrls: ['./bill.component.scss'],
  providers: [
    {provide: MAT_DATE_LOCALE, useValue: 'fr'},
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    {provide: MAT_DATE_FORMATS, useValue: MAT_MOMENT_DATE_FORMATS},
  ],
})
export class BillComponent implements OnInit {
  @ViewChild('iframe') iframe: ElementRef;
  service_id: string;
  bill_id : string;
  date = new Date();
  client_id; 
  awsUrl = environment.awsUrl;
  client: any ;
  paid_status = false;
  billTotalht: any = 0;
  billTotalttc: any = 0;
  service: any ;
  tva : any = 0 ;
  rs : any = 0;
  rsPercent : any = 5;
  bill: any = {};
  config: any = {};
  type  :any;
  form : FormGroup ;
  payForm : FormGroup;
  debours = [] ;
  debour : any = "0.000";
  bills  :any ;
  payments = [];
  creator = this.authservice.getIdfromToken();
  constructor(private activatedRoute: ActivatedRoute,
              private adminService: AdminService,
              private mService: ManagementService,
              private employeService: EmployeeService,
              private authservice: AuthService,
              private fb: FormBuilder,
              private router: Router,
              private cService :ConfigurationService,
              private changeDetectorRef: ChangeDetectorRef,
              public notificationService: NotificationService,
              private snackBar: MatSnackBar)
  {
    this.form = this.fb.group({
      libelle: ['', Validators.required],
      montant: ['0.000',Validators.required],
     
    });
        
    this.payForm = this.fb.group({
      type:new FormControl('2',Validators.required),
      numero: [''],
      rib: [''],
      date: [''],
      bank : '',
      payd : 0,
      myBank :'',
      selectedBank :'',
      ref : '',
      createdAt : new Date(),
    });
  }


  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params => {
      this.service_id = params['service_id'];
      this.client_id = params['client_id'];
      this.type = params['type'];
      this.bill_id = params['bill_id'];
      this.fetchclient();
      this.fetchBill();

    });
    }
  
  setPayment(){
    this.paid_status = !this.paid_status;
    if(this.paid_status){
      document.getElementById("openModalButton").click();
    }
  }


  getConfig(){
    this.cService.getConfig()
    .subscribe(res => {
      if(res.length > 0){
      this.config = res[0].structure.find(one => one._id == this.client.structure) ;
  }
    }, err => {
      console.log(err);
    });
  }


  paySubmit(){
    let pay = {
      payment : this.payForm.value,
      paid_status : true,
    }
    console.log(this.bill);
    if(this.bill_id){
    this.employeService.updateBillPayment(this.bill_id, pay).subscribe(data => {
      if (data.success) {
        console.log(data);
        this.snackBar.open('Paiement effectu', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
      } else {
        console.log(data);
        this.snackBar.open('Une erreur s\'est produite', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
      }
     });
    }else{
      this.bill.payment = this.payForm.value ;
      this.paid_status = true;
      this.snackBar.open('Paiement effectu', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
      document.getElementById('closePayment').click();
      console.log(this.bill);
    console.log(this.bill.payment);
  }
}

  checkCash(){
   return Number(this.billTotalttc.toString().replace(/\s/g, '')) <= 5000 ;
  }

  deleteDebour(i){
    this.snackBar.open('montant supprimer', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
    let x = Number(this.debours[i].montant.replace(/\s/g, ''));
    this.billTotalttc = Number(this.billTotalttc.replace(/\s/g, ''));
    this.debour = Number(this.debour.replace(/\s/g, ''));
    this.debour -= x;
    this.billTotalttc -= x;
    this.debours.splice(i,1);
    this.debour = Number(this.debour.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
    this.billTotalttc = Number(this.billTotalttc.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
    
    
  }

  addDebour(){
    this.debours.push(this.form.value);
    this.billTotalttc = Number(this.billTotalttc.replace(/\s/g, ''));
    this.debour = Number(this.debour.replace(/\s/g, ''));
    this.debour += Number(this.form.controls.montant.value.replace(/\s/g, ''));
    this.billTotalttc += Number(this.form.controls.montant.value.replace(/\s/g, ''));
    this.form.reset();
    this.form.controls.montant.patchValue("0.000");
    this.debour = Number(this.debour.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
    this.billTotalttc = Number(this.billTotalttc.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
  }
  confirmDebour(){

   this.form.reset();
   this.form.controls.montant.patchValue("0.000");
   if(this.bill_id){
    let data = {
      debour : this.debour,
      debours : this.debours
    }
   this.employeService.updateDebour(this.bill_id,data).subscribe(data2 => {
    if (data2.success) {
      console.log('done');
    } else {
      console.log(data2.msg);
    }
   });
  }
  }
  printBill() {
    let printContents, popupWin;
    printContents = document.getElementById('bill').innerHTML;
    popupWin = window.open('', '_blank', 'top=0,left=50%,height=' + screen.height + ',width=' + screen.width);
    popupWin.document.open();
    popupWin.document
    .write(`<html>
     ${$('head').clone().html()}
    <body>${printContents}</body></html>`);
    popupWin.document.close();
    popupWin.print();
    setTimeout(function(){popupWin.close(); },3000);
    
    }

    sendToClient() {
     this.router.navigate(['/services'], { queryParams: { id: this.client_id } });
      }

    stringAsDate(dateStr: string) {
      return new Date(dateStr);
    }

    savebill(send) {
      if(this.billTotalht != '0.000'){
      this.bill.amount = this.billTotalht;
      console.log(this.tva.trim());
      this.bill.vat =Number(this.tva.replace(/\s/g, ''));
      console.log(this.bill.vat);
      this.bill.rs = Number(this.rs.replace(/\s/g, ''));
      this.bill.type= this.type;
      this.bill.rsPercent = this.rsPercent ;
      this.bill.debour = Number(this.debour.replace(/\s/g, ''));
      this.bill.debours = this.debours;
      this.bill.paid_status = this.paid_status;
      this.bill.creatorId = this.creator;
      this.bill.service = this.service._id;
      this.bill.client = this.client_id;
      this.bill.sent = send;
      console.log(this.bill);
      this.employeService.createbill(this.bill).subscribe(data => {
        if (data.success) {
          if(send){
            //notif client
            let notifLinkClt = {
              url : '/home'
            };
            let notification = {
              'sender': this.authservice.getIdfromToken(),
              'receiver': this.client_id,
              'title': 'Nouvelle facture',
              'image': 'invoice.png',
              'message':'Votre facture numero \"'+ data.obj.numero+'\" a t cr',
              'link': notifLinkClt,
            };
              this.notificationService.createNotification(JSON.stringify(notification)).subscribe(data2 => {
                if (data2.success) {
                  console.log(notification);
                  console.log(data2);
                }
                });
          }
          this.mService.UpdateBilledStatus(this.service_id).subscribe(data2 => {
            if (data2.success) {
              console.log(data2.msg);
              this.router.navigate(['/services'], { queryParams: { id: this.client_id } });
            } else {
              console.log(data2.msg);
            }
           });
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        } else {
            this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
        }
       });
       
      }else{
        this.snackBar.open('Montant incorrect', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
      }
      }

    fetchclient() {
        this.adminService.getOneClient(this.client_id)
        .subscribe(res => {
          this.client = res ;
          this.getConfig();
          this.payForm.patchValue({
            rib : this.client.rib,
            bank : this.client.bank,
          })
          console.log(this.client);
        }, err => {
          this.router.navigate(['/services'], { queryParams: { id: this.client_id } });
          console.log(err);
      });
    }

    fetchBill() {
      if (this.service_id) {
        this.employeService.getbillByClientId(this.client_id)
        .subscribe(res => {
          if(res)
          this.bills = res.filter(bill => bill.service == this.service_id) ;          
            if (!this.bill_id) {
              this.fetchService();
            } else {     
              this.fetchLastBill();
              }
        }, err => {
         this.router.navigate(['/services'], { queryParams: { id: this.client_id } });
          console.log(err);
      });
      }

    }


  

    fetchService() {

      if (this.service_id) {
      this.mService.getOneService(this.service_id)
        .subscribe(res => {
          console.log(res);
          this.service = res;
          this.debour = 0 ;
          this.service.montantFixed = this.service.montant ;
          if(this.type == 1){
          this.service.montant = 0 ;
          this.service.montant =Number(this.service.montant.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
          this.service.name = "Avance "+this.service.name;
          }else{
            this.service.montant = Number(this.service.montant.replace(/\s/g, ''));
            this.bills.forEach(bill => {
                this.service.montant -= Number(bill.amount.replace(/\s/g, '')); 
              
            });
            console.log(this.service.montant);
            this.service.montant =Number(this.service.montant.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
          }
          this.billTotalht += Number(this.service.montant.replace(/\s/g, ''));

          if(this.type == 0)
          this.service.montant = Number(this.service.montantFixed.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '); 
          
          this.tva = (this.billTotalht / 100) * 13;
     
          this.billTotalttc = this.billTotalht + this.tva;
          this.rs = (this.billTotalttc / 100) * this.rsPercent ;
          this.billTotalttc = this.billTotalht + this.tva + this.debour - this.rs;
          this.debour = Number(this.debour.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '); ;
          this.tva = Number(this.tva.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
          this.rs = Number(this.rs.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
          this.billTotalttc +=Number("0.60".replace(/\s/g, ''));
          this.billTotalttc = Number(this.billTotalttc.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
          this.billTotalht = Number(this.billTotalht.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
        }, err => {
          console.log(err);
      });
      } else {
        this.router.navigate(['/services'], { queryParams: { id: this.client_id } });
      }
    }


    fetchLastBill(){
      this.mService.getOneService(this.service_id)
      .subscribe(res => {
        console.log(res);
        this.service = res;
        this.debour = 0 ;
        this.service.montantFixed = this.service.montant ;
        if(this.type == 1){
          this.service.name = "Avance "+this.service.name;
          let bill = this.bills.find(bill => bill._id == this.bill_id) ;
        this.paid_status = bill.paid_status ;
        if(bill.payment){
        this.bill.payment = bill.payment;
        this.payForm.patchValue(bill.payment);
        }
        this.bill.numero = bill.numero;
        this.bill.createdAt = bill.createdAt;
        this.service.montant =  bill.amount ;
        this.debours = bill.debours ;
        this.debour = bill.debour ;
        this.changeDetectorRef.detectChanges();
        }else{
          this.service.montant =  Number(this.service.montant.replace(/\s/g, ''));
          this.bills.forEach(bill => {
            if(bill.type == 1){
            this.service.montant -= Number(bill.amount.replace(/\s/g, ''));
          }else{
          bill.debours.forEach(deb => {
            this.debours.push(deb);
            this.debour += Number(deb.montant) ;
          });
          if(bill.payment){
            this.paid_status = bill.paid_status ;
            this.bill.payment = bill.payment;
            this.payForm.patchValue(bill.payment);
            }
            this.bill.numero = bill.numero;
            this.bill.createdAt = bill.createdAt;

        }
          });
          this.service.montant =Number(this.service.montant.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');

        }
        console.log(this.service.montant);
        this.billTotalht += Number(this.service.montant.replace(/\s/g, ''));
        if(this.type == 0)
        this.service.montant = Number(this.service.montantFixed.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '); ;

         this.tva = (this.billTotalht / 100) * 13;
     
        this.billTotalttc = this.billTotalht + this.tva;
        this.rs = (this.billTotalttc / 100) * this.rsPercent ;
        this.billTotalttc = this.billTotalht + this.tva + this.debour - this.rs;
        this.debour = Number(this.debour.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '); ;
        this.tva = Number(this.tva.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
        this.rs = Number(this.rs.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
        this.billTotalttc +=Number("0.60".replace(/\s/g, ''));
        this.billTotalttc = Number(this.billTotalttc.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
        this.billTotalht = Number(this.billTotalht.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
      }, err => {
        console.log(err);
    });
    }




    numericOnly(event): boolean {
      const patt =  /^\d*\.?\d*$/;
      return patt.test(event.key);
    }
    estwi(event) {
      const input = <HTMLInputElement>event.srcElement;
       input.value =  Number(input.value.replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
      }

    updateService(id: number, property: string, event: any) {
      let unPaidAmount = 0;
      let editField =  <HTMLInputElement>event.srcElement.value.toString();
      let x : string =  Number(editField.toString().replace(/\s/g, '')).toString();
      this.bills.forEach(bill => {
        unPaidAmount += Number(bill.amount.toString().replace(/\s/g, '')) ;
      });
      console.log(editField);
      console.log(x);
      console.log(unPaidAmount);
     if(((Number(this.service.montantFixed.toString().replace(/\s/g, '')) - unPaidAmount) >= Number(x) && this.type == 0) ||
        ((Number(this.service.montantFixed.toString().replace(/\s/g, '')) - unPaidAmount) > Number(x) && this.type == 1)
      ){
    
      console.log(editField);
      console.log(x);
      x =  Number(x.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
      this.service[property] = x;
      this.billTotalht  = 0;
      /*this.services.forEach(service => {
        this.billTotalht += Number(service.montant.replace(/\s/g, ''));
      });*/
      this.billTotalht += Number(this.service.montant.replace(/\s/g, ''));
      this.tva = (this.billTotalht / 100) * 13;
      this.rs = (this.billTotalht / 100)*this.rsPercent;
      this.billTotalttc = this.billTotalht + this.tva +this.rs+ Number("0.60".replace(/\s/g, ''));
      this.debour = 0 ;
      this.debours.forEach(debour => {
        this.billTotalttc += Number(debour.montant.replace(/\s/g, ''));
        this.debour += Number(debour.montant.replace(/\s/g, ''));
      });
      this.debour = Number(this.debour.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');

      this.rs = Number(this.rs.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
      this.tva = Number(this.tva.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
      this.billTotalttc = Number(this.billTotalttc.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
      this.billTotalht = Number(this.billTotalht.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
      console.log("ttc:"+this.billTotalttc + " ht:"+ this.billTotalht);
    }else{
      const input = <HTMLInputElement>event.srcElement;
      input.value = this.service.montant ;
      if(this.type == 1){
        this.snackBar.open("Vous ne pouvez pas mettre le montant total dans une avance sur note d'honoraires", 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
      }else{
      this.snackBar.open("Montant invalide", 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
    }
    }
    }

    updateDebour() {
      console.log(this.form.value);
      this.form.controls.montant.patchValue(Number(this.form.controls.montant.value.replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '));
      console.log(this.form.value);

    }

    updateRs() {
      if(this.rsPercent == ''){
        this.rsPercent= 0;
      }
        console.log( Number(this.rs.replace(/\s/g, ''))*Number(this.rsPercent)+' '+this.rsPercent);
        this.billTotalttc = Number(this.billTotalttc.replace(/\s/g, ''));
        this.billTotalttc -= Number(this.rs.replace(/\s/g, ''));
        console.log(this.billTotalttc);
        this.rs = (Number(this.billTotalttc.replace(/\s/g, '')) / 100)*Number(this.rsPercent);
        console.log(this.rsPercent+' '+this.rs);
        this.billTotalttc -= this.rs ;
        this.rs = Number(this.rs.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
        this.billTotalttc = Number(this.billTotalttc.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
      
      console.log(this.rs);
      console.log("ttc:"+this.billTotalttc + " ht:"+ this.billTotalht);
    }





}

