import { Component, OnInit , Inject, ViewEncapsulation, Renderer2, OnDestroy  } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import {emailValidation, passValidation} from '../../../services/validations';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MatSlideToggle } from '@angular/material/slide-toggle';
import { MatSnackBar } from '@angular/material/snack-bar';
import {Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../../../environments/environment';
import {AdminService} from '../../../services/admin/admin.service';
import {EmployeeService} from '../../../services/employee/employee.service';
import {AuthService} from '../../../services/auth/auth.service';
import {ConfigurationService} from '../../../services/configuration/configuration.service';
import { CustomValidators } from './../../../services/custom-validators';

const uri = environment.baseUrl;

@Component({
  selector: 'app-client-action',
  templateUrl: './client-action.component.html',
  styleUrls: ['./client-action.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ClientActionComponent implements OnInit, OnDestroy {
  form: FormGroup ;
  public client: any ;
  param: string
  id: string;
  users: any;
  filteredUsers: any;
  structures: any;
  filteredStructures: any;
  showPw: boolean = false;
  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    private router: Router,
    public dialogRef: MatDialogRef<ClientActionComponent>,
    private adminService: AdminService,
    private cService: ConfigurationService,
    private employeeService: EmployeeService,
    private authservice: AuthService,
    private fb: FormBuilder,
    private snackBar: MatSnackBar,
    @Inject(MAT_DIALOG_DATA) public data: any

    ) {
      this.fetchusers();
      this.getStructures();
      this.form = this.fb.group({
        pm: [{value: false, disabled: this.data.id}],
        name: [''],
        mat_fis: [''],
        fname: ['', Validators.required],
        lname: ['', Validators.required],
        email: new FormControl('', [
          Validators.required,
          emailValidation,
        ]),
        adress: [''],
        ref: ['', Validators.required],
        tel: new FormControl('', [
          Validators.min(10000000),
          Validators.max(99999999)
        ]),
        fax: new FormControl('', [
          Validators.min(10000000),
          Validators.max(99999999)
        ]),
        rib : new FormControl('', [
          Validators.minLength(20),
          Validators.maxLength(20),
          Validators.pattern('^[0-9]*$')
        ]),
        bank : '',
        password: [
          null,
          Validators.compose([
            Validators.required,
            // check whether the entered password has upper case letter
            CustomValidators.patternValidator(/[A-Z]/, {
              hasCapitalCase: true
            }),
            // check whether the entered password has a special character
            CustomValidators.patternValidator(
              /[ !@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/,
              {
                hasSpecialCharacters: true
              }
            ),
            Validators.minLength(8)
          ])
        ],
        cpassword: new FormControl('', [
          passValidation,
        ]),
        porteur:   '',
        structure: ['', Validators.required]
      });
    }

    ngOnDestroy(): void {
        if(this.authservice.introJS){
          this.renderer.removeClass(this.document.body, 'embedded-body-client');
         }
      }

    ngOnInit() {
      if(this.authservice.introjs)
      this.renderer.addClass(this.document.body, 'embedded-body-client');
     this.id = this.data.id
     if (this.id) {
      this.form.controls['pm'].setValidators([Validators.required]);
       this.adminService.getOneClient(this.id)
       .subscribe(res => {
         this.client = res ;
         this.form.patchValue({
             pm : this.client.pm,
             adress : this.client.adress,
             ref : this.client.ref,
             email : this.client.email,
             name : this.client.name,
             fname : this.client.fname,
             lname : this.client.lname,
             tel : this.client.tel,
             mat_fis: this.client.mat_fis,
             fax: this.client.fax,
             porteur: this.client.porteur,
             structure: this.client.structure
         });
         if(this.client.rib){
          this.form.patchValue({
            rib : this.client.rib,
            bank : this.client.bank,
        });
         }
         this.form.controls['password'].clearValidators();
         this.form.controls['password'].setValidators([
          // check whether the entered password has upper case letter
          CustomValidators.patternValidator(/[A-Z]/, {
            hasCapitalCase: true
          }),
          // check whether the entered password has a special character
          CustomValidators.patternValidator(
            /[ !@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/,
            {
              hasSpecialCharacters: true
            }
          ),
          Validators.minLength(8)
         ]);

         this.form.controls['password'].updateValueAndValidity();
       }, err => {
          console.log(err);
      });
     } else{
      if(this.authservice.introjs){      
        setTimeout(() =>{
          
          this.authservice.introJS.goToStep(11).start();
        }, 500);          
          window.addEventListener('keydown',(e) =>{
            if(e.keyCode==13){
               
               this.authservice.introJS.goToStep(11).start();
              }
            },true);

      }
    }
 }


 numericOnly(event): boolean {
  const patt =  /^\d*\.?\d*$/;
  return patt.test(event.key);
}

 onSubmit(){
   this.toggled();
   if(this.form.valid) {
   if(this.id) {
    if (this.form.controls.password.value == '') {
      this.form.removeControl('password');
      }
    
   this.adminService.updateClient(this.id, this.form.value).subscribe(data => {
    if (data.success) {
      this.snackBar.open('Mise  jours avec succs', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
      this.dialogRef.close();
    } else {
      console.log(data);
      this.snackBar.open('Une erreur c\'est produite', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
    }
   });
  } else {
    this.adminService.registerClient(this.form.value).subscribe(data => {
      if (data.success) {
        this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        this.dialogRef.close();  
        if(this.authservice.introjs){      
          setTimeout(() =>{
            
            this.authservice.introJS.goToStep(12).start();
          }, 500);
        }
        this.router.navigate(['/services'], { queryParams: { id: data.obj }});
      } else {
        this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
      }
     });
  }
 }
}


getStructures(){
  this.cService.getConfig()
  .subscribe(res => {
    console.log(res);
    if(res.length > 0){
      console.log(res);
      console.log(res[0].structure);
      this.structures = res[0].structure;
      this.filteredStructures = this.structures.slice();  
}
  }, err => {
    console.log(err);
  });
}


fetchusers(){
  this.employeeService.getUsers()
  .subscribe(res => {
    res.forEach(user => {
      user.name = user.fname + ' ' + user.lname;      
    });
    this.users = res.filter(user => user.grade > 3);
    this.filteredUsers = this.users.slice();

  }, err => {
    console.log(err);
  });
}

 toggled() {
  if (!this.form.controls.pm.value) {
    this.form.controls['fname'].setValidators([Validators.required]);
    this.form.controls['lname'].setValidators([Validators.required]);
    this.form.controls['name'].clearValidators();
    this.form.controls['name'].updateValueAndValidity();
  } else {
    this.form.controls['name'].setValidators([Validators.required]);
    this.form.controls['fname'].clearValidators();
    this.form.controls['fname'].updateValueAndValidity();
    this.form.controls['lname'].clearValidators();
    this.form.controls['lname'].updateValueAndValidity();
  }
  this.form.updateValueAndValidity();
 }

 onNoClick(): void {
  this.dialogRef.close();
}

}
