import { Component, OnInit,ViewChild } from '@angular/core';
import {ConfigurationService} from '../../services/configuration/configuration.service';
import {AuthService} from '../../services/auth/auth.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import {FormGroup, FormBuilder, FormControl, Validators, FormGroupDirective} from '@angular/forms';
import {emailValidation, cinValidation, ribValidation, passValidation} from '../../services/validations' ;

@Component({
  selector: 'app-configuration',
  templateUrl: './configuration.component.html',
  styleUrls: ['./configuration.component.scss']
})
export class ConfigurationComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  MyDataSource: any;
  displayedColumns: string[] = ['nom','rib','adresse','action'];
  form : FormGroup;
  banques = [];
  banqueId : any;
  constructor(
    private cService: ConfigurationService,
    private snackBar: MatSnackBar,
    private fb: FormBuilder,
    public authservice: AuthService
  ) {
    this.form = this.fb.group({
      nom: [''],
      rib:  new FormControl('', [
        Validators.required,
        ribValidation
      ]),
      adresse: [''],
    });
   }

  ngOnInit() {
    if(this.authservice.introjs){      
      setTimeout(() =>{
        let el = document.getElementById("step1");
        el.scrollIntoView();
        //
        this.authservice.introJS.goToStep(2).start();
      }, 500);
    }
    this.fetchBanques();
  }


  applyFilter(filterValue){
    this.MyDataSource.filter = filterValue.trim().toLowerCase();
  }


  deleteBanque(){
    if(this.banqueId){
      this.cService.deleteBanque(this.banqueId)
      .subscribe(res => {
        this.fetchBanques();
        this.form.reset();
        this.banqueId = '';
        this.snackBar.open('Banque supprimer', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        }, (err) => {
          console.log(err);
        }
      );
    }
  }

  setBanque(id){
    this.banqueId = id;
    let banque = this.banques.find(one => one._id == id);
    this.form.patchValue(banque);
  }
  
  fetchBanques(){
    this.cService.getAllBanques()
    .subscribe(res => {
    this.banques = res ;
    console.log(this.banques);
    this.MyDataSource = new MatTableDataSource(this.banques);
    this.MyDataSource.paginator = this.paginator;
    this.MyDataSource.sort = this.sort;
    }, err => {
      console.log(err);
    });
  }

  onSubmit(formDirective : FormGroupDirective){
    if(this.banqueId) {
      this.cService.updateBanque(this.banqueId, this.form.value).subscribe(data => {
       if (data.success) {
         this.fetchBanques();
         this.form.reset();
         formDirective.resetForm();
         this.banqueId = '';
         this.snackBar.open('Mise  jours avec succs', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
       } else {
         this.snackBar.open('Une erreur s\'est produite', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
       }
      });
     } else {
       this.cService.createBanque(this.form.value).subscribe(data => {
         if (data.success) {
           this.fetchBanques();
           this.form.reset();
           formDirective.resetForm();
           if(this.authservice.introjs){
             
             this.authservice.introJS.goToStep(3).start();
             }
           this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
       } else {
         this.snackBar.open(data.msg, 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
         }
        });
     }
  }

  stringAsDate(dateStr: string) {
    return new Date(dateStr);
  }  

}
