import { Component, OnInit ,Input,Output} from '@angular/core';
import {ManagementService} from '../../../services/management/management.service';
import {EmployeeService} from '../../../services/employee/employee.service';
import {NotificationService} from '../../../services/notification/notification.service';
import {ConfigurationService} from '../../../services/configuration/configuration.service'
import {AdminService} from '../../../services/admin/admin.service';
import {AuthService} from '../../../services/auth/auth.service';
import {FormGroup, FormBuilder, FormControl, Validators, FormArray, FormGroupDirective} from '@angular/forms';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { environment } from '../../../../environments/environment';
import {Router, ActivatedRoute } from '@angular/router';
import { EventEmitter } from '@angular/core';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';

@Component({
  selector: 'app-modele-service',
  templateUrl: './modele-service.component.html',
  styleUrls: ['./modele-service.component.scss']
})
export class ModeleServiceComponent implements OnInit {
  form : FormGroup;
  formTask : FormGroup;
  tasks = [];
  showCreateTask = false;
  taskId : any;
  departs = [];
selectedDepart : any;  Math = Math;
@Input() toDepart : string;
@Output("parentFun") parentFun: EventEmitter<any> = new EventEmitter();

    constructor(
      private adminService: AdminService,
      private employeeService : EmployeeService,
      private cService: ConfigurationService,
      private fb: FormBuilder,
      private snackBar: MatSnackBar,
      public authservice: AuthService,
      private mService : ManagementService,
      private activatedRoute: ActivatedRoute,
      private router: Router,
      private notificationService : NotificationService,
    ) {
  
      this.form = this.fb.group({
        depart: ['', Validators.required],
        modelName: ['', Validators.required],
        tasks:'',
      });

      this.formTask = this.fb.group({ 
        depart: ['', Validators.required],
        task: ['', Validators.required],
        time: ['', Validators.required],
        repeat:false,
      });    
  
     }
  
     ngOnDestroy(){
      this.toDepart = '';
    }
     
    ngOnInit() {
 if(this.authservice.introjs){
 this.authservice.introJS.goToStep(14).start();
 }
 this.fetchDeparts();
  if(this.toDepart){
    this.selectedDepart = this.toDepart;
    this.form.controls.depart.patchValue(this.toDepart);
    this.fetchTasks();
   }
 
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.form.controls.tasks.value, event.previousIndex, event.currentIndex);
  }

  getTask(one){
    return this.tasks.find(task => task._id == one) ;
  }


  setUpdateTask(id){
    this.showCreateTask = true;
    this.taskId = id;
    let task = this.getTask(id);
    this.formTask.patchValue({
      depart: task.depart,
      task: task.task,
      time: task.time,
      repeat:task.repeat
    });
  }


    fetchDeparts(){
      this.cService.getAllDeparts()
      .subscribe(res => {
      this.departs = res ;
      }, err => {
        console.log(err);
      });
    }

    fetchTasks(){
      this.mService.getTasks()
      .subscribe(res => {
        this.tasks = res.filter(task => task.departement == this.form.get('depart').value) ;
      }, err => {
        console.log(err);
      });
    }
  



    onSubmitTask(formDirective : FormGroupDirective){
      this.formTask.controls.depart.patchValue(this.form.get('depart').value);   
        this.mService.updateTask(this.taskId,this.formTask.value).subscribe(data => {
          if (data.success) {
            this.formTask.reset();
            formDirective.resetForm();
            this.taskId ='';
            this.fetchTasks();
            this.snackBar.open('Mise  jours avec succs', 'X', {
                 duration: 6000,
                 verticalPosition: 'top',
                 panelClass:['custom-snack'],
               });
          } else {
            console.log(data);
            this.snackBar.open(data.msg, 'X', {
                 duration: 6000,
                 verticalPosition: 'top',
                 panelClass:['custom-snack-error'],
               });
          }
         });
        }


    numericOnly(event): boolean {
      const patt =  /^\d*\.?\d*$/;
      return patt.test(event.key);
    }
  
  
    createModeleService(formDirective : FormGroupDirective){
      console.log(this.form.value);
      let x = [];
      this.form.controls.tasks.value.forEach(element => {
        x.push(element);
      });
      this.form.controls.tasks.patchValue(x);
      this.mService.createServiceModel(this.form.value).subscribe(data => {
        if (data.success) {
          // let tab : HTMLElement = document.getElementById('newService_tog') as HTMLElement;
          // tab.click();
          this.form.reset();
          formDirective.resetForm();
          if(this.toDepart)
          this.form.controls.depart.patchValue(this.toDepart);
          this.parentFun.emit();
          this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
          if(this.authservice.introjs)
          this.authservice.introJS.goToStep(15).start();
    
        } else {
          console.log(data);
          this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
        }
       });
    }
  
  }
  