import { Component, OnInit , ChangeDetectorRef, ViewChild, ElementRef } from '@angular/core';
import { FileUploader } from 'ng2-file-upload';
import { environment } from '../../../../environments/environment';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import {EmployeeService} from '../../../services/employee/employee.service';
import {AppeldoffreService} from '../../../services/appeldoffre/appeldoffre.service';
import {saveAs} from 'file-saver';
import {Router, ActivatedRoute } from '@angular/router';
import {FormGroup, FormBuilder, Validators, FormControl} from '@angular/forms';
import {NotificationService} from '../../../services/notification/notification.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';

const amazoUrl = environment.awsUrl;
const uri = environment.uri;

export interface modelDoc{
  docName?:string;
  docPath?:string;
}
export interface linkModel{
  url? : string ;
  params? : string ;
  paramsValue?:string ;
}

@Component({
  selector: 'app-modele-appel',
  templateUrl: './modele-appel.component.html',
  styleUrls: ['./modele-appel.component.scss']
})
export class ModeleAppelComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild('closeBtn') closeBtn: ElementRef;
  @ViewChild('closeBtnCat') closeBtnCat: ElementRef;
  public uploader: FileUploader = new FileUploader({url: uri, removeAfterUpload: true , allowedFileType: ['image', 'doc', 'pdf', 'xls', 'ppt']});
  public filePreviewPath: SafeUrl;
  public hasBaseDropZoneOver:boolean = false;
  public hasAnotherDropZoneOver:boolean = false;
  public id:any ;
  documents : Array<modelDoc> = [];
  userId : any ;
  ModeleForm : FormGroup;
  catForm : FormGroup;
  url =  environment.baseUrl;
  categories: any;
  allRefs = [];
  isNotUnique = false;
  users: any;
  filteredCategories: any; 
  filteredUsers: any; 
  groupes = [];
  doc = [] ;
  submited = false;
  removing = false;
  MyDataSource: any;
  displayedColumns: string[] = ['categorie','filename', 'action'];
  isRef = false;
  type = 2;
  data: any;
  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private sanitizer: DomSanitizer,
    private emplyeeService : EmployeeService,
    private appeldoffreService : AppeldoffreService,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private notificationService : NotificationService,
    private router: Router,
    private snackBar: MatSnackBar,
  ) {

    
    this.ModeleForm = this.fb.group({
      category: ['', Validators.required]
    });

    this.catForm = this.fb.group({
      catName: ['', Validators.required]
    });

    this.fetchCategories();
  
    this.uploader.onBeforeUploadItem = (item) => {
      item.withCredentials = false;
     
    }
    this.uploader.onAfterAddingFile = (fileItem) => {
      this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
    }
   }

  ngOnInit() {


    this.fetchData();
  }

  onSubmit(){
    if(this.ModeleForm.valid && this.uploader.queue.length > 0 ){
      this.submited = true;
       
      if (this.uploader.queue.length > 0) {
        for(var i =0;i<this.uploader.queue.length;i++){
        this.uploader.queue[i].upload()
        this.uploader.onCompleteItem = (item: any, response: any , status: any, headers: any) => {
          if(response)
          this.doc.push(JSON.parse(response).uploadname);
          else{
            this.snackBar.open('Un erreur c\'est produit verifier votre connection ', 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack-error'],
           });
          }
        }
      }
        }

      this.uploader.onCompleteAll = () => {
        let document = this.ModeleForm.value;
        document.path = this.doc;
        document.isRef = this.isRef;
        this.appeldoffreService.addDocument(document).subscribe(data => {
          if (data.success) {
            this.fetchData();
            this.ModeleForm.reset();
            this.submited = false;
            this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
          } else {
            console.log(data);
            this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
          }
         });
      }

    }else{
      this.snackBar.open('Verifier les champs', 'X', {
         duration: 6000,
         verticalPosition: 'top',
         panelClass:['custom-snack-error'],
       });
    }
}

  public fileOverBase(e:any):void {
    this.hasBaseDropZoneOver = e;
  }
 
  public fileOverAnother(e:any):void {
    this.hasAnotherDropZoneOver = e;
  }

  rotateArrow(section,action){
    let files : HTMLElement = document.getElementById(section) as HTMLElement;
    if(!files.classList.contains('down')){
      files.classList.add('down') ;
    }else{
      files.classList.remove('down');
    }

  }

  fetchCategories(){
    this.appeldoffreService.getCategories()
    .subscribe(res => {
     this.categories = res ;
     this.filteredCategories = this.categories.slice();
    }, err => {
      console.log(err);
    });
  }

  addCategory(){
    if(this.catForm.valid){
      this.appeldoffreService.addCategorie(this.catForm.value).subscribe(data => {
        console.log(data);
        if (data.success) {
          this.catForm.reset();
          this.closeBtnCat.nativeElement.click();
          this.fetchCategories();
          this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
        }else {
          this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack-error'],
             });
        }
       });
    }else{
      this.snackBar.open('Verifier les champs', 'X', {
         duration: 6000,
         verticalPosition: 'top',
         panelClass:['custom-snack-error'],
       });
    }
  }

  removeFile(item){
    this.removing = true;
    item.remove();
    setTimeout(()=>{    
      this.removing = false;
    }, 500); 
    this.changeDetectorRef.detectChanges();
  }

  downloadTask(path){
    this.appeldoffreService.downloadDoc(path)
    .subscribe(
      data => saveAs(data,path.split('.')[1]),
      error => console.error(error)
    );
   }

   fetchData(){
    this.appeldoffreService.getDocuments()
    .subscribe(res => {
      console.log(res);
      this.MyDataSource = new MatTableDataSource();
      res.forEach(doc => {
        doc.cat = '';
        this.categories.forEach(cat => {
          if(doc.category.includes(cat._id)){
            doc.cat = doc.cat + '| ' + cat.name;
          }
          
        });
      });
      this.data = res;
      this.MyDataSource.data = res;
      
      this.MyDataSource.paginator = this.paginator;
      this.MyDataSource.sort = this.sort;
    }, err => {
      console.log(err);
    });
  }

  filterData(){
    if(this.type === 2){
      this.MyDataSource.data = this.data;
    }else{
      this.MyDataSource.data = this.data.filter(
        one => one.isRef == this.type);  
    }
    this.MyDataSource.paginator = this.paginator;
    this.MyDataSource.sort = this.sort;
    console.log( this.MyDataSource.data);
    console.log( this.data);

      
  }

  getCategories(){
    this.appeldoffreService.getCategories()
    .subscribe(res => {
     this.categories = res ;
     console.log(res);
    }, err => {
      console.log(err);
    });
  }

applyFilter(filterValue: string) {
  this.MyDataSource.filter = filterValue.trim().toLowerCase();
}



}
