import { Component, OnInit , Inject,ChangeDetectorRef, ViewChild, ElementRef, ViewEncapsulation, Renderer2, OnDestroy  } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { FileUploader } from 'ng2-file-upload';
import { environment } from '../../../../environments/environment';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import {EmployeeService} from '../../../services/employee/employee.service';
import {AppeldoffreService} from '../../../services/appeldoffre/appeldoffre.service';
import {AuthService} from '../../../services/auth/auth.service';
import {Router, ActivatedRoute } from '@angular/router';
import {FormGroup, FormBuilder, Validators, FormControl} from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import {NotificationService} from '../../../services/notification/notification.service';

const amazoUrl = environment.awsUrl;
const uri = environment.uri;

export interface modelDoc{
  docName?:string;
  docPath?:string;
}
export interface linkModel{
  url? : string ;
  params? : string ;
  paramsValue?:string ;
}

@Component({
  selector: 'app-add-appel',
  templateUrl: './add-appel.component.html',
  styleUrls: ['./add-appel.component.scss']
})
export class AddAppelComponent implements OnInit, OnDestroy {
  @ViewChild('closeBtn') closeBtn: ElementRef;
  @ViewChild('closeBtnCat') closeBtnCat: ElementRef;
  public uploader: FileUploader = new FileUploader({url: uri, removeAfterUpload: true , allowedFileType: ['image', 'doc', 'pdf', 'xls', 'ppt']});
  public filePreviewPath: SafeUrl;
  public hasBaseDropZoneOver:boolean = false;
  public hasAnotherDropZoneOver:boolean = false;
  public id:any ;
  documents : Array<modelDoc> = [];
  public notification : any ;
  public receiverId : any = [] ;
  public notifLink : linkModel = {} ;
  userId : any ;
  appeldoffreform : FormGroup;
  catForm : FormGroup;
  url =  environment.baseUrl;
  categories: any;
  allRefs = [];
  isNotUnique = false;
  users: any;
  filteredCategories: any; 
  filteredUsers: any; 
  groupes = [];
  docList = [] ;
  submited = false;
  removing = false;
  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    private changeDetectorRef: ChangeDetectorRef,
    private sanitizer: DomSanitizer,
    private emplyeeService : EmployeeService,
    private appeldoffreService : AppeldoffreService,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private notificationService : NotificationService,
    private router: Router,
    public authservice: AuthService,
    private snackBar: MatSnackBar,
  ) {

    this.fetchRefs();
    
    this.appeldoffreform = this.fb.group({
      ste: ['', Validators.required],
      ref: ['', Validators.required],
      title: ['', Validators.required],
      category: ['', Validators.required],
      users: ['', Validators.required],
      commentaire: [''],
    });

    this.catForm = this.fb.group({
      catName: ['', Validators.required]
    });

    this.fetchCategories();
  
    this.uploader.onBeforeUploadItem = (item) => {
      item.withCredentials = false;
     
    }
    this.uploader.onAfterAddingFile = (fileItem) => {
      this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
    }
   }
   ngOnDestroy(): void {
    if(this.authservice.introJS){
      this.renderer.removeClass(this.document.body, 'embedded-body-appel');
     }
  }

ngOnInit() {
  if(this.authservice.introjs){
  this.renderer.addClass(this.document.body, 'embedded-body-appel');  
  setTimeout(() =>{
    
    this.authservice.introJS.goToStep(22).start();
  }, 500);     
}
    this.fetchUsers();


  }

  onSubmit(){
    this.closeBtn.nativeElement.click();
    if(this.appeldoffreform.valid && this.uploader.queue.length > 0 && !this.isNotUnique){
      this.submited = true;
        if (this.uploader.queue.length > 0) {
          for(var i =0;i<this.uploader.queue.length;i++){
          this.uploader.queue[i].upload()
          this.uploader.onCompleteItem = (item: any, response: any , status: any, headers: any) => {
            if(response)
            this.docList.push(JSON.parse(response).uploadname);
            else{
             this.snackBar.open('Une erreur s\'est produite, Vrifier votre connection', 'X', {
              duration: 6000,
              verticalPosition: 'top',
              panelClass:['custom-snack-error'],
             });
            }
          }
        }
          }
      
      let appeldoffre = this.appeldoffreform.value;
      appeldoffre.files = this.docList;
      appeldoffre.createdBy = this.emplyeeService.getIdfromToken();
      appeldoffre.users.push(this.emplyeeService.getIdfromToken());
      console.log(appeldoffre);

      this.uploader.onCompleteAll = () => {
        this.appeldoffreService.addAppel(appeldoffre).subscribe(data => {
          if (data.success) {
            this.notifLink.url = '/editAppel';
            this.notifLink.params = 'id' ;
            this.notifLink.paramsValue = data.obj ;
            appeldoffre.users.pop();
              this.notification = {
                'sender': this.emplyeeService.getIdfromToken(),
                'receiver': appeldoffre.users,
                'title': 'Appele d\'offre',
                'image': this.emplyeeService.getImagefromToken(),
                'message': 'Vous tes affecter  '+appeldoffre.title,
                'link': this.notifLink,
              };

              this.snackBar.open(data.msg, 'X', {
                 duration: 6000,
                 verticalPosition: 'top',
                 panelClass:['custom-snack'],
               });
            this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
              console.log(data2);
              if (data2.success) {
                this.router.navigate(['/editAppel'], { queryParams: { id: data.obj } });
                if(this.authservice.introjs){
				  
                  this.authservice.introJS.goToStep(26).start();
                }
                this.submited = false;
              }else {
               this.snackBar.open('Une erreur s\'est produite', 'X', {
                duration: 6000,
                verticalPosition: 'top',
                panelClass:['custom-snack-error'],
               });
              }
             });
          } else {
             this.snackBar.open(data.msg, 'X', {
              duration: 6000,
              verticalPosition: 'top',
              panelClass:['custom-snack-error'],
             });
          }
         });
      }

    }else{
         this.snackBar.open('Vrifier les champs', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
    }
}

   nowStringAsDate() {
    return new Date();
  }
  public fileOverBase(e:any):void {
    this.hasBaseDropZoneOver = e;
  }
 
  public fileOverAnother(e:any):void {
    this.hasAnotherDropZoneOver = e;
  }

  rotateArrow(section,action){
    let files : HTMLElement = document.getElementById(section) as HTMLElement;
    if(!files.classList.contains('down')){
      files.classList.add('down') ;
    }else{
      files.classList.remove('down');
    }

  }

  checkUnique(){

        if (this.allRefs.includes(this.appeldoffreform.get('ste').value.toUpperCase()+this.appeldoffreform.get('ref').value.toUpperCase()) ) {
            // The array contains the search string
            this.isNotUnique = true;
        }
        else {
            // Search string not found
            this.isNotUnique = false;
        }
  }

  fetchRefs(){
    this.appeldoffreService.getRefs()
    .subscribe(res => {
      this.allRefs = res.map(function(value) {
        return (value._id.ste+value._id.ref).toUpperCase();
      });
    }, err => {
      console.log(err);
    });
  }

  fetchCategories(){
    this.appeldoffreService.getCategories()
    .subscribe(res => {
     this.categories = res ;
     this.filteredCategories = this.categories.slice();
    }, err => {
      console.log(err);
    });
  }

  fetchUsers(){
    this.appeldoffreService.getGroupes()
    .subscribe(res => {
      this.groupes = res;
    this.emplyeeService.getUsers()
    .subscribe(res => {
     res.forEach(user => {
       user.name = user.fname + ' ' + user.lname;
     });
     this.users = res;
     let havePermissions : Array<any> = [] ;
  
    this.users.forEach(user => {
      user.permissions.forEach(permission => {
        let group = this.groupes.filter(group => group._id == permission)[0];
        group.permissions.forEach(element => {
          if(element == 'appels'){
          havePermissions.push(user);
          return ;
          }
        });
      });
    });
    havePermissions = havePermissions.filter(function(elem, index, self) {
      return index === self.indexOf(elem);
  })
  this.filteredUsers = havePermissions;
    }, err => {
      console.log(err);
    });
  }, err => {
    console.log(err);
  });
}

  havePermission(role){
    let havePermissions : Array<any> = [] ;
  
    this.users.forEach(user => {
      user.permissions.forEach(permission => {
        let group = this.groupes.filter(group => group._id == permission.id)[0];
        group.permissions.forEach(element => {
          if(element == role){
          havePermissions.push(user);
          return ;
          }
        });
      });
    });
    havePermissions = havePermissions.filter(function(elem, index, self) {
      return index === self.indexOf(elem);
  })
  return havePermissions ;
  }

  addCategory(){
    if(this.catForm.valid){
      this.appeldoffreService.addCategorie(this.catForm.value).subscribe(data => {
        console.log(data);
        if (data.success) {
          this.catForm.reset();
          this.closeBtnCat.nativeElement.click();
          if(this.authservice.introjs){	  
            this.authservice.introJS.goToStep(25).start();
          }
          this.fetchCategories();
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
       } else {
         this.snackBar.open(data.msg, 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
        }
       });
    }else{
         this.snackBar.open('Vrifier les champs', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
    }
  }

  removeFile(item){
    this.removing = true;
    item.remove();
    setTimeout(()=>{    
      this.removing = false;
    }, 500); 
    this.changeDetectorRef.detectChanges();
  }

  goTo24(){
    if(this.authservice.introjs){
      setTimeout(() =>{
        console.log('heere');
        this.authservice.introJS.goToStep(24).start();
      }, 500);     
    }
  }

}
