import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import {AdminService} from '../../services/admin/admin.service' ;
import {AuthService} from '../../services/auth/auth.service' ;
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { environment } from '../../../environments/environment' ;
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';


const uri = environment.uri;


@Component({
  selector: 'app-convention',
  templateUrl: './convention.component.html',
  styleUrls: ['./convention.component.scss']
})
export class conventionComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild('closeModal', {static: true} ) closeModal: ElementRef;
  hasBaseDropZoneOver:boolean = false;
  hasAnotherDropZoneOver:boolean = false;
  submited = false;
  removing = false;
  form: FormGroup ;
  deleteId :string ;
  uri = environment.baseUrl;
  mypermissions: any[];
  MyDataSource: any;
  displayedColumns: string[] = ['Type','Description','Adresse','Action'];
  docList = [] ;

confirmationId :string ;

  constructor(
              private adminService: AdminService,
              public authservice: AuthService,
              private fb: FormBuilder,
              private snackBar: MatSnackBar,) { 
    
                  this.form = this.fb.group({
                    type: ['', Validators.required],
                    description: '',
                    adresse: '',
                   });
              }

  ngOnInit() {
    this.fetchData();
  
  }
  
  fetchData(){
    this.adminService.getConventions()
        .subscribe(res => {
        this.MyDataSource = new MatTableDataSource();
        this.MyDataSource.data = res;
        this.MyDataSource.paginator = this.paginator;
        this.MyDataSource.sort = this.sort;
  }, err => {
    console.log(err);
  });

  }

  togglModal(id){
    this.deleteId = id;
  }
  delete(id){
 
      this.adminService.deleteConvention(id)
        .subscribe(res => {
            this.fetchData();
            let element :HTMLElement = document.getElementById('hideModal') as HTMLElement;
            element.click();
            this.snackBar.open('Convention supprim avec succs', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
          }, (err) => {
            console.log(err);
          }
        );

    }

  onSubmit(){
    if(this.form.valid){
      this.submited = true;

      let convention = this.form.value;
      this.adminService.addConvention(convention).subscribe(data => {
        if (data.success) {
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
          document.getElementById('closeModal').click();
          this.form.reset();
          this.fetchData();
        } else {
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
        }
        this.submited = false;
       });
    }else{
      this.snackBar.open('Verifier les champs', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
      this.submited = false;
    }
}

    applyFilter(filterValue: string) {
      this.MyDataSource.filter = filterValue.trim().toLowerCase();
    }
  
}
