import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import {AdminService} from '../../services/admin/admin.service';
import {ManagementService} from '../../services/management/management.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ClientActionComponent } from './client-action/client-action.component';
import { animate, state, style, transition, trigger } from '@angular/animations';
import {EmployeeService} from '../../services/employee/employee.service';
import {AuthService} from '../../services/auth/auth.service';
import {merge, of as observableOf} from 'rxjs';
import {map, startWith, switchMap} from 'rxjs/operators';

export interface Aclient  {
  id: string;
  active :boolean;
  name: string;
  fname: string;
  lname: string;
  adress: string;
  email: string;
}

@Component({
  selector: 'app-clients',
  templateUrl: './clients.component.html',
  styleUrls: ['./clients.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0', visibility: 'hidden'})),
      state('expanded', style({height: '*', visibility: 'visible'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class ClientsComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  aclientList: Aclient[];
  public clients : any;
  clientsId : any ;
  permissions : any ;
  // Important objects
  services: any ;
  counter = 0;
  tableSort = -1;
  expandedElement: Aclient | null;
  MyDataSource: any;
  displayedColumns: string[] = ['fname', 'adress', 'email', 'num', 'action'];
  users = [] ;
  data = [];
  filter = '';
  isLoadingResults = true;
  totalDocs = 0 ;
  public deleteId: string ;
  constructor(
    private employeeService: EmployeeService,
    private mService: ManagementService,
    public authservice: AuthService,
    private adminService: AdminService,
    private snackBar: MatSnackBar,
    private dialog: MatDialog,
    ) {}

  ngOnInit() {
    if(this.authservice.introjs){      
      setTimeout(() =>{
        
        this.authservice.introJS.goToStep(10).start();
      }, 500);
    }
    this.fetchUsers();
    this.fetchServices();

  }

  ngAfterViewInit() {
    this.sort.sortChange.subscribe(() =>{    
        this.tableSort *= -1 ;
    });
    this.loadData('no-query');
  }

  togglModal(id) {
    this.deleteId = id;
  }


  fetchUsers(){
    this.employeeService.getUsers()
    .subscribe(res => {
      this.users = res ;
    }, err => {
      console.log(err);
    });
  }
 

  delete(id) {
    this.adminService.deleteClient(id)
      .subscribe(res => {
          this.fetchData('no-query');
          console.log(res);
          const element: HTMLElement = document.getElementById('hideModal') as HTMLElement;
          element.click();
          this.snackBar.open(res, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        }, (err) => {
          console.log(err);
        }
      );
  }


  openForm(clientId = null) {
    const configDialog = new MatDialogConfig();
    configDialog.width = '65%';
    configDialog.panelClass = 'custom-dialog-container' ;
    configDialog.data = {
      id: clientId,
  };
  const dialogRef = this.dialog.open(ClientActionComponent, configDialog);
    dialogRef.afterClosed().subscribe(result => {
    this.fetchData('no-query');
  });

  }
  
  loadData(query){
    merge(this.sort.sortChange, this.paginator.page)
    .pipe(
      startWith({}),
      switchMap(() => {
        this.isLoadingResults = true;
        return this.fetchData(query);
      }),
      map(data => {
        this.isLoadingResults = false;     
        this.totalDocs = data.totalDocs ;
        return data.docs;
      })
    ).subscribe(data => this.MyDataSource = new MatTableDataSource(data));
  }

  fetchData(query){
    return this.adminService.getClientsToTable(this.paginator.pageIndex+1,this.paginator.pageSize,this.tableSort,query);
  }



  fetchServices(){
    this.mService.getServices()
    .subscribe(res => {
      this.services = [] ;
      this.clientsId = [];
      res.forEach(element => {
        if(this.authservice.permissions.includes(element.depart+'Manager') || element.clientConsult == this.employeeService.getIdfromToken()){
        this.services.push(element);
        }else{
        element.tasks.forEach(task => {
          if(task.user == this.employeeService.getIdfromToken() && !this.services.find(one => one._id == element._id)){
            this.services.push(element);
          }
        });
        }
      });
      this.fetchData('no-query');
    }, err => {
      console.log(err);
    });
  }

  applyFilter(filterValue: string) {
    if(filterValue == '')
    filterValue = 'no-query';
    this.paginator.pageIndex = 0;
    this.loadData(filterValue);

  }

  avenceService(tasks){
    let completed = tasks.filter(i => i.status == 'completed');
    return completed.length / tasks.length * 100 ;
  }

  stringAsDate(dateStr: string) {
    return new Date(dateStr);
  }
  returncounter(): number{
    return this.counter;
  }
  counterinc(){
    this.counter++;
  }
  counterzero(){
    this.counter = 0;
  }

}
