import { AdminService } from './../../../services/admin/admin.service';
import { Component, OnInit, OnChanges, Input } from '@angular/core';
import {ManagementService} from '../../../services/management/management.service';
import {EmployeeService} from '../../../services/employee/employee.service';
import { TasksComponent } from '../tasks/tasks.component';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.scss']
})

export class ServicesComponent implements OnInit {
  public users: any ;
  public clients: any ;
  public services = [] ;
  public tasks: any ;
 superId = this.employeeService.getIdfromToken();

  constructor(
    private employeeService: EmployeeService,
    private adminService: AdminService,
    private mService: ManagementService,
  ) { }
  @Input() id: string;

  ngOnInit() {
    this.fetchUsers();
    this.fetchServices();
    this.fetchClients();
  }
  ngOnChanges() {
      this.ngOnInit();
    }
  fetchServices() {
      this.mService.getServices()
      .subscribe(res => {
        this.services = [];
        this.services = res.filter(
          service => {
            if (service.depart == this.id && service.tasks.length > 0) {
            return  (service.tasks.some(task => task.user === this.employeeService.getIdfromToken()) || service.clientConsult === this.employeeService.getIdfromToken() ) ;
            }
          });

      }, err => {
        console.log(err);
      });

  }


  fetchUsers() {
    this.employeeService.getUsers()
    .subscribe(res => {
      this.users = res ;
    }, err => {
      console.log(err);
    });
  }

  fetchClients() {
    this.adminService.getClients()
    .subscribe(res => {
      this.clients = res ;
    }, err => {
      console.log(err);
    });
  }
  avenceService(tasks) {
    const completed = tasks.filter(i => i.status === 'completed');
    return (completed.length / tasks.length * 100).toFixed(2);
  }

  stringAsDate(dateStr: string) {
    return new Date(dateStr);
  }
 
}

