import { Component, OnInit , Inject, ViewEncapsulation, Renderer2, OnDestroy  } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import {Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../../../environments/environment';
import {AdminService} from '../../../services/admin/admin.service';
import {AuthService} from '../../../services/auth/auth.service';
import {ConfigurationService} from '../../../services/configuration/configuration.service';
import { async } from '@angular/core/testing';

const uri = environment.baseUrl;
@Component({
  selector: 'app-groupe-action',
  templateUrl: './groupe-action.component.html',
  styleUrls: ['./groupe-action.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class GroupeActionComponent implements OnInit, OnDestroy{
  permissionslist: string[][] = [
                                ['Crer', 'Create'],
                                ['Mise  jours', 'Update'],
                                ['Voir', 'View'],
                                ['Supprimer', 'Delete']];
  form: FormGroup ;
  dynamicForm: FormGroup ;
  public groupe: any ;
  param: string
  id: string;
  departs: any;
  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    public dialogRef: MatDialogRef<GroupeActionComponent>,
    private adminService: AdminService,
    private authservice: AuthService,
    private configurationService: ConfigurationService,
    private fb: FormBuilder,
    private snackBar: MatSnackBar,
    @Inject(MAT_DIALOG_DATA) public data: any

    ) {    
      this.form = this.fb.group({
        groupename: ['', Validators.required],
        appels: [''],
        encaissement: [''],
        decaissement: [''],
        reglement: [''],
        bill: [''],
        users: [''],
        dashboard: [''],
        clients: [''],
        groupes: [''],
        rh: [''],
        config: [''],
      });
    }

    ngOnDestroy(): void {
      if(this.authservice.introJS){
      this.renderer.removeClass(this.document.body, 'embedded-body');
      
      this.authservice.introJS.goToStep(7).start();
    }

  }

    ngOnInit() {
      if(this.authservice.introjs)
      this.renderer.addClass(this.document.body, 'embedded-body');
     this.id = this.data.id;
     if (this.id) { 
          this.configurationService.getAllDeparts()
          .subscribe( async res => {    
          this.departs = res;    
            let group={}
            res.forEach(depart=>{
              group[depart._id] = new FormControl('');  
              this.form.addControl(depart._id,new FormControl(''))
            })
            console.log(res);
          this.adminService.getOneGroupe(this.id)
          .subscribe(res => {
            this.groupe = res ;
            Object.keys(this.form.controls).forEach(key => {
              this.form.controls[key].setValue(this.filterItems(res.permissions, key));
            });
            this.form.controls['groupename'].setValue(res.groupename);
          }, err => {
              console.log(err);
          });
        }, err => {
          console.log(err);
        });
     }else{
      this.configurationService.getAllDeparts()
      .subscribe( async res => {    
      this.departs = res;    
        let group={}
        res.forEach(depart=>{
          group[depart._id] = new FormControl('');  
          this.form.addControl(depart._id,new FormControl(''))
        });
        if(this.authservice.introjs){      
          setTimeout(() =>{
            
            this.authservice.introJS.goToStep(6).start();
          }, 500);
        }
      }, err => {
        console.log(err);
      });
       
     }
 }

 onSubmit() {
  if (this.id){
   if (!this.form.invalid) {
   this.adminService.updateGroupe(this.id, this.form.value).subscribe(data => {
    if (data.success) {
      this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
      this.dialogRef.close();
    } else {
        this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
    }
   });
  }
 } else {
   // create new groupe
   if (!this.form.invalid) {
   this.adminService.createGroupe(this.form.value).subscribe(data => {
    if (data.success) {
      this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
      this.dialogRef.close();
    } else {
        this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
    }
    if(this.authservice.introjs){      
        
        this.authservice.introJS.goToStep(7).start();
    }
   });
  }
 }

 }
 filterItems = (arr, query) => {
  return arr.filter(el => el.toLowerCase().indexOf(query.toLowerCase()) > -1);
};

selectAll(control,suffix, permissionslist){
  
  let values = [suffix+'Manager']; 
  permissionslist.forEach(el => {
    values.push(suffix + '' + el[1]);
  });
    console.log(this.form.controls[control].value.length);
  if (this.form.controls[control].value.length > 1) {
    this.form.controls[control].setValue(null);
  } else {
    this.form.controls[control]
      .setValue(values);
  }
  
}

}
