import { Component, OnInit , ChangeDetectorRef, ViewChild, ElementRef, ViewEncapsulation } from '@angular/core';
import {formatDate} from '@angular/common';
import { FileUploader } from 'ng2-file-upload';
import { environment } from '../../../../environments/environment';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import {EmployeeService} from '../../../services/employee/employee.service';
import {AppeldoffreService} from '../../../services/appeldoffre/appeldoffre.service';
import {saveAs} from 'file-saver';
import {AuthService} from '../../../services/auth/auth.service';

import {Router, ActivatedRoute } from '@angular/router';
import {FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import {NotificationService} from '../../../services/notification/notification.service';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { MatDatepicker } from '@angular/material/datepicker';
import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';

const amazoUrl = environment.awsUrl;
const uri = environment.uri;

export interface modelDoc{
  status?:string;
  docPath?:string;
}
export interface linkModel{
  url? : string ;
  params? : string ;
  paramsValue?:string ;
}

@Component({
  selector: 'app-edit-appel',
  templateUrl: './edit-appel.component.html',
  styleUrls: ['./edit-appel.component.scss'],
  providers: [
    {provide: MAT_DATE_LOCALE, useValue: 'fr'},
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    {provide: MAT_DATE_FORMATS, useValue: MAT_MOMENT_DATE_FORMATS},
  ]
})
export class EditAppelComponent implements OnInit {
  @ViewChild('closeBtn') closeBtn: ElementRef;
  @ViewChild('fileInput') fileInputElement: ElementRef;
  public uploader: FileUploader = new FileUploader({url: uri, removeAfterUpload: true , allowedFileType: ['image', 'doc', 'pdf', 'xls', 'ppt']});
  public filePreviewPath: SafeUrl;
  public hasBaseDropZoneOver:boolean = false;
  public hasAnotherDropZoneOver:boolean = false;
  public id:any ;
  documents : Array<modelDoc> = [];
  public notification : any ;
  public receiverId : any = [] ;
  public notifLink : linkModel = {} ;
  userId : any ;
  appeldoffreform : FormGroup;
  formulaireform : FormGroup;
  mofdaliteForm : FormGroup;
  url =  environment.baseUrl ;
  categories: any;
  modalites: any;
  users: any;
  commentaire = '';
  selectedFile: any;
  appeldoffre: any;
  filteredCategories: any; 
  filteredModalites: any; 
  filteredUsers: any; 
  groupes = [];
  docList : Array<modelDoc> = [];
  Submiting = false;
  removing = false;
  filesOnHold:any = [];
  filesAccepted:any = [];
  filesRejected:any = [];
  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private sanitizer: DomSanitizer,
    public authservice: AuthService,
    private emplyeeService : EmployeeService,
    private appeldoffreService : AppeldoffreService,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private notificationService : NotificationService,
    private router: Router,
    private snackBar: MatSnackBar,
  ) {
    this.appeldoffreform = this.fb.group({
      title         : [{value: '', disabled: true}, Validators.required],
      category      : [{value: '', disabled: true}, Validators.required],
      users         : [{value: '', disabled: true}, Validators.required],
      commentaire   : [{value: '', disabled: true}, Validators.required]
    });

    this.formulaireform = this.fb.group({
      date_depot    : ['', Validators.required],
      mode_depot    : ['', Validators.required],
      adresse_depot : this.fb.array([]),
      provision     : [false, Validators.required],
      bank          : [''],
      montant       : ['']
    });

    this.mofdaliteForm = this.fb.group({
      modName: ['', Validators.required]
    });

    this.uploader.onBeforeUploadItem = (item) => {
      item.withCredentials = false;
     
    }
    this.uploader.onAfterAddingFile = (fileItem) => {
      this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
    }
   }

   get adresse_depot() {
    return this.formulaireform.get('adresse_depot') as FormArray;
  }

 removeItem() {
    this.adresse_depot.removeAt(this.adresse_depot.length - 1);
 }
  
  addAdress() {
    this.formulaireform.controls.mode_depot.value.forEach(id => {
      console.log(this.adresse_depot.value);
      if(!this.adresse_depot.value.find(adresse => adresse.idDepot == id)){
      this.adresse_depot.push(
        this.fb.group({
          name : this.modalites.find(one => one._id == id).name,
          idDepot : [id,Validators.required],
          adresse :['',Validators.required],
          }) 
      );
        }
    });
    this.adresse_depot.value.forEach((adr,index) => {
      console.log(this.formulaireform.controls.mode_depot.value.indexOf(adr.idDepot));
      if(this.formulaireform.controls.mode_depot.value.indexOf(adr.idDepot) == -1){
      this.adresse_depot.removeAt(index);
      }
    });
   console.log(this.formulaireform.value)
  }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params => {
      this.id = params['id'];
    if (this.id) {
      this.fetchCategories();
      this.fetchModalite();
      this.fetchUsers();  
      this.getOffre();
    } else {
      this.router.navigate(['/appeldoffre']);

    }
  });


  }

  onChange(event){
    this.addAdress();
  }

  getOffre(){
    this.appeldoffreService.getOneAppel(this.id)
      .subscribe(res => {
        if(!res)
        this.router.navigate(['/appeldoffre']);
        this.appeldoffre = res;
        console.log(this.appeldoffre);
        res.filesCreated.forEach(file => {
          if(file.status == 0){
            this.filesOnHold.push(file);
          }
          else if(file.status == 1){
            this.filesAccepted.push(file);
          } else {
            this.filesRejected.push(file);
          }
        });
        if(res.commentaire.length<1)
        res.commentaire = 'Aucun commentaire';
        this.appeldoffreform.patchValue({
          title       : res.titre,
          category    : res.category,
          users       : res.users,
          commentaire : res.commentaire,
          });
          if(res.formulaire){
            let date = new Date(res.formulaire.date_depot);
            date.setHours(date.getHours() + 1);
            this.formulaireform.patchValue({
                date_depot    : date.toISOString().substring(0, 16),
                mode_depot    : res.formulaire.mode_depot,
                adresse_depot : res.formulaire.adresse_depot,
                provision     : res.formulaire.provision,
                bank          : res.formulaire.bank,
                montant       : res.formulaire.montant
              });
          }

      }, err => {
        this.router.navigate(['/user-profile'], { queryParams: { id: this.emplyeeService.getIdfromToken()},
        skipLocationChange: true} );
      });
  }

toggleModal(file){
  this.commentaire = ''
  this.selectedFile = file;
}
  fetchModalite(){
    this.appeldoffreService.getModalites()
    .subscribe(res => {
     this.modalites = res ;
     this.filteredModalites = this.modalites.slice();
    }, err => {
      console.log(err);
    });
  }

  resetAdresses(){
    while (this.adresse_depot.length !== 0) {
      this.adresse_depot.removeAt(0)
    }
  }

  toggled() {
    if (!this.formulaireform.controls.provision.value) {
      this.formulaireform.controls['bank'].setValidators([Validators.required]);
      this.formulaireform.controls['montant'].setValidators([Validators.required]);
    } else {
      this.formulaireform.controls['bank'].clearValidators();
      this.formulaireform.controls['bank'].updateValueAndValidity();
      this.formulaireform.controls['montant'].clearValidators();
      this.formulaireform.controls['montant'].updateValueAndValidity();
    }
    this.formulaireform.updateValueAndValidity();
   }

  formulaireSubmit(){
    console.log(this.formulaireform.value);
    if(this.formulaireform.valid){
      this.Submiting = true;
      
      let appeldoffre = {
        status : '1',
        formulaire : this.formulaireform.value,
      };
  
      console.log(appeldoffre);

        this.appeldoffreService.updateAppel(this.id,appeldoffre).subscribe(data => {
          if (data.success) {
            this.notifLink.url = '/editAppel';
            this.notifLink.params = 'id' ;
            this.notifLink.paramsValue = this.id ;

              this.notification = {
                'sender': this.emplyeeService.getIdfromToken(),
                'receiver': this.users,
                'title': 'Appele d\'offre',
                'image': this.emplyeeService.getImagefromToken(),
                'message': 'Status d\'appel d\'offre est change',
                'link': this.notifLink,
              };

            this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
            this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
              console.log(data2);
              if (data2.success) {                            
                this.getOffre();
                this.Submiting = false;
              }else {
                this.snackBar.open('Erreur notification Contacter xpr.com.tn!!!', 'X', {
                  duration: 6000
                });
              }
             });
          } else {
            console.log(data);
            this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
          }
         });
    }else{
      this.snackBar.open('Verifier les champs', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
    }
  }

   StringAsDate(date) {
    return new Date(date);
  }
  public fileOverBase(e:any):void {
    this.hasBaseDropZoneOver = e;
  }
 
  public fileOverAnother(e:any):void {
    this.hasAnotherDropZoneOver = e;
  }

  rotateArrow(section,action){
    let files : HTMLElement = document.getElementById(section) as HTMLElement;
    if(!files.classList.contains('down')){
      files.classList.add('down') ;
    }else{
      files.classList.remove('down');
    }

  }

  fetchCategories(){
    this.appeldoffreService.getCategories()
    .subscribe(res => {
     this.categories = res ;
     this.filteredCategories = this.categories.slice();
    }, err => {
      console.log(err);
    });
  }


  fetchUsers(){
    this.appeldoffreService.getGroupes()
    .subscribe(res => {
      this.groupes = res;
    this.emplyeeService.getUsers()
    .subscribe(res => {
     res.forEach(user => {
       user.name = user.fname + ' ' + user.lname;
     });
     this.users = res;
     let havePermissions : Array<any> = [] ;
  
    this.users.forEach(user => {
      user.permissions.forEach(permission => {
        let group = this.groupes.filter(group => group._id == permission)[0];
        group.permissions.forEach(element => {
          if(element == 'appels'){
          havePermissions.push(user);
          return ;
          }
        });
      });
    });
    havePermissions = havePermissions.filter(function(elem, index, self) {
      return index === self.indexOf(elem);
  })
  this.filteredUsers = havePermissions;
    }, err => {
      console.log(err);
    });
  }, err => {
    console.log(err);
  });
}

  havePermission(role){
    let havePermissions : Array<any> = [] ;
  
    this.users.forEach(user => {
      user.permissions.forEach(permission => {
        let group = this.groupes.filter(group => group._id == permission.id)[0];
        group.permissions.forEach(element => {
          if(element == role){
          havePermissions.push(user);
          return ;
          }
        });
      });
    });
    havePermissions = havePermissions.filter(function(elem, index, self) {
      return index === self.indexOf(elem);
  })
  return havePermissions ;
  }

  addModalite(){
    if(this.mofdaliteForm.valid){
      this.appeldoffreService.addModalite(this.mofdaliteForm.value).subscribe(data => {
        console.log(data);
        if (data.success) {
          this.mofdaliteForm.reset();
          this.closeBtn.nativeElement.click();
          this.fetchModalite();
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
       } else {
         this.snackBar.open(data.msg, 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
        }
       });
    }else{
      this.snackBar.open('Verifier les champs', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
         });
    }
  }

  removeFile(item){
    this.removing = true;
    item.remove();
    setTimeout(()=>{    
      this.removing = false;
    }, 500); 
    this.changeDetectorRef.detectChanges();
  }

  showActions(id){
    let actions : HTMLElement = document.getElementById(id) as HTMLElement;
    if(actions.className !="")
    actions.className = "";
    else
    actions.className= "actions";
  }

  downloadTask(file){
    this.appeldoffreService.downloadDoc(file.docPath)
    .subscribe(
      data => saveAs(data,file.docPath.split('.')[1]),
      error => console.error(error)
    );
   }

   downloadsimple(file){
    this.appeldoffreService.downloadDoc(file)
    .subscribe(
      data => saveAs(data,file.split('.')[1]),
      error => console.error(error)
    );
   }

   numericOnly(event): boolean {
    const patt =  /^\d*\.?\d*$/;
    return patt.test(event.key);
  }
  estwi(event) {
    const tmp = Number(event.target.value.replace(/\s/g, '')).toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
    this.formulaireform.controls['montant'].setValue(tmp);
  
    this.formulaireform.controls.montant.updateValueAndValidity()
  }

  senfFiles(){
    if(this.uploader.queue.length > 0){
      this.docList = [];
      this.Submiting = true;
        if (this.uploader.queue.length > 0) {
          for(var i =0;i<this.uploader.queue.length;i++){
          this.uploader.queue[i].upload()
          this.uploader.onCompleteItem = (item: any, response: any , status: any, headers: any) => {
            if(response){
              let document : modelDoc = {};
              document.status = '0';
              document.docPath = JSON.parse(response).uploadname;
              this.docList.push(document); 
            }else{
              this.snackBar.open('Un erreur c\'est produit verifier votre connection ', 'X', {
                duration: 6000,
                verticalPosition: 'top',
                panelClass:['custom-snack-error'],
               });
            }
          }
        }
          }
      
      let appeldoffre = {
        filesCreated : this.docList,
      }
      console.log(appeldoffre);

      this.uploader.onCompleteAll = () => {
        this.appeldoffreService.addFiles(this.id,appeldoffre).subscribe(data => {
          if (data.success) {
            this.notifLink.url = '/editAppel';
            this.notifLink.params = 'id' ;
            this.notifLink.paramsValue = this.id ;

              this.notification = {
                'sender': this.emplyeeService.getIdfromToken(),
                'receiver': this.users,
                'title': 'Appele d\'offre',
                'image': this.emplyeeService.getImagefromToken(),
                'message': 'Fichier(s) Ajouter',
                'link': this.notifLink,
              };

            this.snackBar.open(data.msg, 'X', {
               duration: 6000,
               verticalPosition: 'top',
               panelClass:['custom-snack'],
             });
            this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
              console.log(data2);
              if (data2.success) {                            
                this.getOffre();
                this.Submiting = false;
                this.uploader.clearQueue();
                this.fileInputElement.nativeElement.value = "";
              }else {
                this.snackBar.open('Erreur notification Contacter xpr.com.tn!!!', 'X', {
                  duration: 6000
                });
              }
             });
          } else {
            console.log(data);
            this.snackBar.open(data.msg, 'X', {
              duration: 6000,
              verticalPosition: 'top',
              panelClass:['custom-snack-error'],
             });
          }
         });
      }

    }else{
      this.snackBar.open('Aucun fuchier dans la queue ', 'X', {
        duration: 6000,
        verticalPosition: 'top',
        panelClass:['custom-snack-error'],
       });
    }
  }

  terminate(status){
        this.Submiting = true;        
        let appeldoffre = {
          status : status,
        };
          this.appeldoffreService.updateAppel(this.id,appeldoffre).subscribe(data => {
            if (data.success) {
              this.notifLink.url = '/editAppel';
              this.notifLink.params = 'id' ;
              this.notifLink.paramsValue = this.id ;
  
                this.notification = {
                  'sender': this.emplyeeService.getIdfromToken(),
                  'receiver': this.users,
                  'title': 'Appele d\'offre',
                  'image': this.emplyeeService.getImagefromToken(),
                  'message': 'Status d\'appel d\'offre est change',
                  'link': this.notifLink,
                };
  
              this.snackBar.open(data.msg, 'X', {
                 duration: 6000,
                 verticalPosition: 'top',
                 panelClass:['custom-snack'],
               });
              this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
                console.log(data2);
                if (data2.success) {                            
                  this.getOffre();
                  this.Submiting = false;
                }else {
                  this.snackBar.open('Erreur notification Contacter xpr.com.tn!!!', 'X', {
                    duration: 6000,
                    verticalPosition: 'top',
                    panelClass:['custom-snack-error'],
                   });
                }
               });
            } else {
              console.log(data);
              this.snackBar.open(data.msg, 'X', {
                duration: 6000,
                verticalPosition: 'top',
                panelClass:['custom-snack-error'],
               });
            }
           });

  }

  taskStatus(file,status){
    this.Submiting = true;        
    file.status = status;
    if (/\S/.test(this.commentaire))
      file.commentaire = this.commentaire;
    else
      file.commentaire = 'Aucun commentaire';   
    
    let appeldoffre = {
      filesCreated : this.appeldoffre.filesCreated
    }
      this.appeldoffreService.updateAppelFiles(this.id,appeldoffre).subscribe(data => {
        if (data.success) {
          this.notifLink.url = '/editAppel';
          this.notifLink.params = 'id' ;
          this.notifLink.paramsValue = this.id ;

            this.notification = {
              'sender': this.emplyeeService.getIdfromToken(),
              'receiver': this.users,
              'title': 'Appele d\'offre',
              'image': this.emplyeeService.getImagefromToken(),
              'message': file.docPath.split('.')[1]+' a t accepter',
              'link': this.notifLink,
            };

          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
          this.notificationService.createNotification(JSON.stringify(this.notification)).subscribe(data2 => {
            console.log(data2);
            if (data2.success) {                            
              this.getOffre();
              this.Submiting = false;
            }else {
              this.snackBar.open('Erreur notification Contacter xpr.com.tn!!!', 'X', {
                duration: 6000
              });
            }
           });
        } else {
          this.snackBar.open(data.msg, 'X', {
            duration: 6000,
            verticalPosition: 'top',
            panelClass:['custom-snack-error'],
           });
        }
       });

  }

  
}
