import { Component, OnInit , Inject, Renderer2, ViewChild, OnDestroy  } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import {EmployeeService} from '../../services/employee/employee.service';
import {AuthService} from '../../services/auth/auth.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import {ManagementService} from '../../services/management/management.service';
import {AdminService} from '../../services/admin/admin.service';

import { animate, state, style, transition, trigger } from '@angular/animations';

@Component({
  selector: 'app-treso',
  templateUrl: './treso.component.html',
  styleUrls: ['./treso.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0', visibility: 'hidden'})),
      state('expanded', style({height: '*', visibility: 'visible'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class TresoComponent implements OnInit, OnDestroy {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  counter = 0;
  expandedElement:  null;
  MyDataSource: any;
  displayedColumns: string[] = ['name','client', 'montant', 'tva', 'debours', 'avance','rap','action'];
  services = [];
  filtre=[] ;
  selectedClients : any; 
  clients =[];
  bills  = [];
  FilterServices = [];
  selectedServices : any ;
  dateDeb : any;
  dateFin : any;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    private employeeService: EmployeeService,
    private mService: ManagementService,
    private adminService : AdminService,
    public authservice : AuthService,
    private snackBar: MatSnackBar,
  ) { }


  ngOnDestroy(): void {
    if(this.authservice.introJS){
      this.renderer.removeClass(this.document.body, 'embedded-body-treso');
     }
  }

  ngOnInit() {
    if(this.authservice.introjs){
      this.renderer.addClass(this.document.body, 'embedded-body-treso');
      setTimeout(() =>{
        
        this.authservice.introJS.goToStep(19).start();
      }, 500);     
    }
    this.fetchData();
  }


  
  fetchData(){
    this.employeeService.getAllBills()
    .subscribe(res => {
    this.bills = res ;
    this.fetchServices();
    }, err => {
      console.log(err);
    });
    
  }


  checkFiltre(){
    let x = this.services;
    if(this.filtre.length > 0){
      if(this.selectedClients && this.selectedClients.length > 0){
      x = x.filter( service =>{
        if(this.selectedClients.includes(service.client))
        return true;
      })
    }
    if(this.selectedServices && this.selectedServices.length > 0){
      x = x.filter( service =>{
        if(this.selectedServices.includes(service.name))
        return true;
      })
    }
     
    }
    if(this.dateDeb && this.dateFin){
     x = x.filter(service => new Date(service.createdAt) >= this.dateDeb &&  new Date(service.createdAt) <= this.dateFin );
    }
    this.MyDataSource = new MatTableDataSource(x);
  }



  fetchServices(){
    this.mService.getServices()
    .subscribe(res => {
    this.services = res ;
          this.adminService.getClients()
          .subscribe(res => {
            this.clients = res ;
            this.services.forEach(service => {
              let client = this.clients.find(clt => clt._id == service.client);
              
              // get client Service 
              if(client.pm)
              service.clientName = client.name ;
              else
              service.clientName = client.fname+" "+client.lname;
              // montant HT & avance & debours..
              let bills = this.bills.filter(bill => bill.service == service._id);
              if(bills){
                service.tva = 0 ;
                service.debour = 0 ;
                service.amountPayd = 0 ;
                bills.forEach(oneBill => {
                service.tva += oneBill.vat ;
                service.debour += oneBill.debour ;
                if(oneBill.paid_status)
                service.amountPayd += Number(oneBill.amount.replace(/\s/g, ''));
                oneBill.vat =Number(oneBill.vat.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
                oneBill.debour =Number(oneBill.debour.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');  
                oneBill.rs =Number(oneBill.rs.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');  

              });
                service.rap = Number((Number(service.montant.replace(/\s/g, ''))-service.amountPayd).toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
                service.tva = Number(service.tva.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
                service.debour = Number(service.debour.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
                service.amountPayd = Number(service.amountPayd.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
                service.montant = Number(service.montant.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
               
              }
              console.log(service);
            });
            this.services.forEach(service => {
              if(!this.FilterServices.includes(service.name))
              this.FilterServices.push(service.name);
            });
            this.MyDataSource = new MatTableDataSource();
            this.MyDataSource.data = this.services;
            this.MyDataSource.paginator = this.paginator;
            this.MyDataSource.sort = this.sort;
           }, err => {
             console.log(err);
           });
    }, err => {
      console.log(err);
    });
    
  }



  stringAsDate(dateStr: string) {
    return new Date(dateStr);
  }

  applyFilter(filterValue){
    this.MyDataSource.filter = filterValue.trim().toLowerCase();

  }



}
