import { Component, OnInit , Inject, Renderer2, ViewChild, OnDestroy  } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import {EmployeeService} from '../../services/employee/employee.service';
import {AuthService} from '../../services/auth/auth.service';
import {AdminService} from '../../services/admin/admin.service';
import {ConfigurationService} from '../../services/configuration/configuration.service';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { MatDatepicker } from '@angular/material/datepicker';
import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
@Component({
  selector: 'app-reglements',
  templateUrl: './reglements.component.html',
  styleUrls: ['./reglements.component.scss'],
  providers: [
    {provide: MAT_DATE_LOCALE, useValue: 'fr'},
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    {provide: MAT_DATE_FORMATS, useValue: MAT_MOMENT_DATE_FORMATS},
  ],
})
export class ReglementsComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  MyDataSource: any;
  clients = [];
  fours = [];
  banques = [];
  decReglements = [];
  type = 2 ;
  payd = [0,1,2];
  selected = 1;
  dateDeb : any;
  dateFin : any;
  totalEnc : any;
  totalDec : any;
  banqueSelected : any;
  encReglements = [];
  displayedColumns: string[] = [];

 
  constructor(
    public authservice : AuthService,
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    private employeeService: EmployeeService,
    private adminService : AdminService,
    private cService : ConfigurationService,
    private snackBar: MatSnackBar,
  ) { 
    this.MyDataSource = new MatTableDataSource();
  }
  
  ngOnDestroy(): void {
    if(this.authservice.introJS){
      this.renderer.removeClass(this.document.body, 'embedded-body-treso');      
     }
  }

  ngOnInit() {
    if(this.authservice.introjs){
      this.renderer.addClass(this.document.body, 'embedded-body-treso');
      setTimeout(() =>{
        
        this.authservice.introJS.goToStep(21).start();
      }, 500);     
    }
    this.fetchFour();
  }
  

  fetchData(){
    this.fetchDecReglements();
    this.fetchEncReglements();
  }

  fetchDecReglements(){ 
    if(this.selected == 0){
    if(this.type == 2)
    this.displayedColumns = ['numero','four','montant','banque','date','action'];
    else{
      if(this.type == 3)
      this.displayedColumns = ['ref','four','montant','banque','action'];
      else
      this.displayedColumns = ['four','montant','banque','date','action'];
    }
  }
  

    this.employeeService.getAllDec()
    .subscribe(tmp => {
     this.decReglements = [];
     let res = []
     if(this.dateDeb && this.dateFin)
     res = tmp.filter(bill => (bill.payment  || bill.trash.length > 0) && new Date(bill.date) >= this.dateDeb &&  new Date(bill.date) <= this.dateFin );
     else
     res = tmp.filter(bill => bill.payment  || bill.trash.length > 0);

     let total = 0;
        res.forEach((pay,index,object) => {
            if(pay.payment.payd == 1)
            total += Number(pay.amount.toString().replace(/\s/g, ''));
          if(pay.payment && pay.payment.type == this.type.toString() && this.payd.includes(pay.payment.payd)){
              let newBill :any  = {};
              newBill.trash = pay.trash;
              newBill._id = pay._id;
              newBill.date = pay.date;
              newBill.amount = pay.amount;
              newBill.payment =  pay.payment ;
              let four = this.fours.find(four => four._id == pay.four);
              newBill.fourName = four.name;
              newBill.payment =  pay.payment ;
              newBill.payment.bankName = this.banques.find(banque => banque._id == pay.payment.bank).nom;
              
              newBill.payment.dateFormat = new Date(newBill.payment.date).toLocaleDateString('fr-FR');

              newBill = {...newBill,...newBill.payment};
              this.decReglements.push(newBill);
        }
        if(pay.trash.length > 0){
          pay.trash.forEach(trash => {
            if(trash.type ==this.type.toString() && this.payd.includes(trash.payd)){
            let newBill :any  = {};
            newBill.trash = [];
            newBill._id = pay._id;
            newBill.date = pay.date;
            newBill.amount = pay.amount;
            newBill.payment =  trash ;
            let four = this.fours.find(four => four._id == pay.four);
            newBill.fourName = four.name;
            newBill.payment.bankName = this.banques.find(banque => banque._id == trash.bank).nom;
            
            newBill.payment.dateFormat = new Date(newBill.payment.date).toLocaleDateString('fr-FR');
            newBill = {...newBill,...newBill.payment};
            console.log(newBill);
            this.decReglements.push(newBill);
            }
          });
        }
      });
      
 
      this.totalDec = Number(total.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '); 
      if(this.selected == 0){
        console.log(this.decReglements);
      this.MyDataSource = new MatTableDataSource(this.decReglements);
      this.MyDataSource.paginator = this.paginator;
      this.MyDataSource.sort = this.sort;
      }
      
    }, err => {
      console.log(err);
    });
  }


  fetchEncReglements(){
    if(this.selected == 1){
    if(this.type == 2)
    this.displayedColumns = ['numero','clt','montant','banque','date','action'];
    else{
      if(this.type == 3)
      this.displayedColumns = ['ref','clt','montant','banque','action'];
      else
      this.displayedColumns = ['clt','montant','banque','date','action'];
    }
  }
    this.employeeService.getAllBills()
    .subscribe(tmp => {
      this.encReglements = [];
      let res = []
      if(this.dateDeb && this.dateFin)
      res = tmp.filter(bill => (bill.payment  || bill.trash.length > 0) && new Date(bill.createdAt) >= this.dateDeb &&  new Date(bill.createdAt) <= this.dateFin );
      else
      res = tmp.filter(bill => bill.payment  || bill.trash.length > 0);

      let total =0;
      res.forEach((pay,index,object) => {
        if(pay.payment.payd)
        total += Number(pay.amount.toString().replace(/\s/g, ''));
        if(pay.payment && pay.payment.type == this.type.toString() && this.payd.includes(pay.payment.payd)){
        if(!pay.payment.myBank){
          pay.payment.selectedBank = this.banques[0]._id ;
        }
            let newBill :any  = {};
            newBill._id = pay._id;
            newBill.trash = pay.trash;
            newBill.createdAt = pay.createdAt;
            newBill.amount = pay.amount;
            newBill.client = pay.client ;
            let client = this.clients.find(clt => clt._id == pay.client);
            if(client.pm){
              newBill.clientName = client.name;
            }
            else{
              newBill.clientName = client.fname+' '+client.lname;
            }
            newBill.payment =  pay.payment ;
            if(pay.payment.myBank){
            newBill.payment.bankName = this.banques.find(banque => banque._id == pay.payment.myBank).nom;
            }
            newBill.payment.dateFormat = new Date(newBill.payment.date).toLocaleDateString('fr-FR');
            newBill = {...newBill,...newBill.payment};
            this.encReglements.push(newBill);
      }
      if(pay.trash.length > 0){
        pay.trash.forEach(trash => {
          if(trash.type ==this.type.toString() && this.payd.includes(trash.payd)){
          let newBill :any  = {};
          newBill.trash = [];
          newBill._id = pay._id;
          newBill.createdAt = pay.createdAt;
          newBill.amount = pay.amount;
          newBill.client = pay.client ;
          newBill.payment =  trash ;
          let client = this.clients.find(clt => clt._id == pay.client);
          if(client.pm){
            newBill.clientName = client.name;
          }
          else{
            newBill.clientName = client.fname+' '+client.lname;
          }
          if(newBill.payment.myBank){
          newBill.payment.bankName = this.banques.find(banque => banque._id == newBill.payment.myBank).nom;
          }
          newBill.payment.dateFormat = new Date(newBill.payment.date).toLocaleDateString('fr-FR');
          console.log(newBill);
          newBill = {...newBill,...newBill.payment};
          this.encReglements.push(newBill);
          }
        });
      }
    });
    this.totalEnc = Number(total.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');  ;
    if(this.selected == 1){
    this.MyDataSource = new MatTableDataSource(this.encReglements);
    this.MyDataSource.paginator = this.paginator;
    this.MyDataSource.sort = this.sort;
    }
    }, err => {
      console.log(err);
    });
  }


  getTotal(type){
    let result = {
      sum :0,
      total :'',
      amount:'',
    } 
    let x = 0;
    let all =0;
    let collection : any;
    if(this.selected)
    collection = this.encReglements ;
    else
    collection = this.decReglements ;

    collection.forEach(reg => {
      if(reg.payd == type){
      result.sum++;
      x += Number(reg.amount.toString().replace(/\s/g, ''));
      }
      all+= Number(reg.amount.toString().replace(/\s/g, ''));
    });
    result.total = Number(x.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
    result.amount = Number(all.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');

    return result;
  }



  applyFilter(filterValue){
    this.MyDataSource.filter = filterValue.trim().toLowerCase();
  }
  
  checkDates(){
    if(this.dateDeb && this.dateFin){
      let x :any;
      if(this.selected == 0)
      x = this.decReglements.filter(service => new Date(service.date) >= this.dateDeb &&  new Date(service.date) <= this.dateFin );
      else
      x = this.encReglements.filter(service => new Date(service.createdAt) >= this.dateDeb &&  new Date(service.createdAt) <= this.dateFin );
      this.MyDataSource = new MatTableDataSource(x); 
    }
   }

  setReglement(bill){
    console.log(bill);
    if(this.selected == 1){
    bill.payment.myBank = bill.payment.selectedBank;
    if(bill.payment.payd == 2){
      bill.paid_status = false ;
      bill.trash.push(bill.payment);
      bill.payment = '';
    }
    this.employeeService.updateBillPayment(bill._id, bill).subscribe(data => {
      if (data.success) {
        console.log(data);
        this.fetchEncReglements();
        this.snackBar.open('Mise ?? jours avec succ??s', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
      } else {
        console.log(data);
        this.snackBar.open('Une erreur s\'est produite', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
      }
     });
    }else{
      bill.payment.myBank = bill.payment.bank;
      if(bill.payment.payd == 2){
        bill.trash.push(bill.payment);
        bill.payment = '';
      }
      let pay = {
        payment : bill.payment,
        trash : bill.trash,
      }
      this.employeeService.updateDec(bill._id, pay).subscribe(data => {
        if (data.success) {
          this.fetchDecReglements();
          this.snackBar.open('Mise ?? jours avec succ??s', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        } else {
          console.log(data);
          this.snackBar.open('Un erreur s\'est produite', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
        }
       });
    }
    console.log(bill);
  }


  stringAsDate(dateStr: string) {
    return new Date(dateStr);
  }
  getClients(){ 
    this.adminService.getClients()
    .subscribe(res => {
      this.clients = res ;
      this.getBanques();
    }, err => {
      console.log(err);
    });
  }

  getMoneyFormat(data){
    return Number(data.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
  }

  getResult(enc,dec){
    let diff = 0 ;
    if(enc && dec){
    diff = Number(enc.toString().replace(/\s/g, '')) -  Number(dec.toString().replace(/\s/g, ''));
    }
    return Number(diff.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
  }

  fetchFour(){
    this.employeeService.getAllFour()
    .subscribe(res => {
    this.fours = res ;
    this.getClients();
    }, err => {
      console.log(err);
    });
    
  }

  getBanques(){ 
    this.cService.getAllBanques()
    .subscribe(res => {
      this.banques = res ;
      this.fetchData();
    }, err => {
      console.log(err);
    });
  }
}
