import { Component, OnInit, Input, ViewChild } from '@angular/core';
import {AdminService} from '../../services/admin/admin.service';
import {EmployeeService} from '../../services/employee/employee.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { DatePipe } from '@angular/common'
import {AuthService} from '../../services/auth/auth.service';
import {NotificationService} from '../../services/notification/notification.service';

@Component({
  selector: 'app-bills',
  templateUrl: './bills.component.html',
  styleUrls: ['./bills.component.scss']
})
export class BillsComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  bills: any;
  client: any;
  users: any;
  payBillid: any;
  payBillnumber: any;
  MyDataSource: any;
  displayedColumns: string[] = ['n', 'createur', 'dateCreation', 'montantTotal', 'status', 'action'];
  @Input() clientid: string;
  constructor(private adminService: AdminService,
              private employeService: EmployeeService,
              private datepipe: DatePipe,
              public authservice: AuthService,
              public notificationService: NotificationService,
              private snackBar: MatSnackBar
    ) {}
  ngOnInit() {
    this.getUsers();
    this.getclient();
  }


  getbill() {
    this.employeService.getbillByClientId(this.clientid)
       .subscribe(res => {
        res.forEach(bill => {
          this.users.forEach(user => {
            if (bill.creatorId === user._id) {
              bill.creatorName = user.lname + ' ' + user.fname;
            }
          });
         });
        this.MyDataSource = new MatTableDataSource();
        this.MyDataSource.data = res;
        this.MyDataSource.paginator = this.paginator;
        this.MyDataSource.sort = this.sort;
        this.bills = res ;
       }, err => {
          console.log(err);
      });
  }

  getclient() {
    this.adminService.getOneClient(this.clientid)
      .subscribe(res => {
        this.client = res ;
      }, err => {
         console.log(err);
     });
  }

  getUsers() {
    this.employeService.getUsers()
       .subscribe(res => {
         this.users = res;
         this.getbill();
       }, err => {
          console.log(err);
      });
  }
  applyFilter(filterValue: string) {
    this.MyDataSource.filter = filterValue.trim().toLowerCase();
  }
  stringAsDate(dateStr: string) {
    return new Date(dateStr);
  }

  togglModal(billid,billnumber) {
    this.payBillid = billid;
    this.payBillnumber = billnumber;
  }

  pay(id) {
    this.employeService.UpdateBillStatus(id)
       .subscribe(data => {
        if (data.success) {
          this.getbill();
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        } else {
            this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        }
       }, err => {
          console.log(err);
      });
  }

  send(id,number) {
    this.employeService.UpdateBillsend(id)
       .subscribe(data => {
        if (data.success) {
          //notif client
            let notifLinkClt = {
              url : '/home'
            };
            let notification = {
              'sender': this.authservice.getIdfromToken(),
              'receiver': this.clientid,
              'title': 'Nouvelle facture',
              'image': 'invoice.png',
              'message':'Votre facture numero \"'+ number+'\" a t cr',
              'link': notifLinkClt,
            };
              this.notificationService.createNotification(JSON.stringify(notification)).subscribe(data2 => {
                if (data2.success) {
                  console.log(notification);
                  console.log(data2);
                }
                });
          this.getbill();
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        } else {
            this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
        }
       }, err => {
          console.log(err);
      });

  }

  print(services){
    let billTotalht: any = 0;
    let billTotalttc: any = 0;
    let printContents, popupWin;
    // tslint:disable-next-line:max-line-length
    printContents = `<div class="container printable" id="bill">
                      <div class="row">
                          <div class="col-md-12">
                          <div class="invoice-title">` +
                             // tslint:disable-next-line:max-line-length
                             ` <h3><a>Date: ` + this.datepipe.transform(this.stringAsDate(services.createdAt), 'yyyy-MM-dd HH:mm')  + `</a><a class="pull-right">Note d'honoraires N` + services.numero + `</a></h3>
                              </div>
                              <hr>
                              <div class="row">
                                <div class="col-md-6">
                                  <address>
                                    <strong>Factur de:</strong><br>
                                    Devlopy Sarl<br>
                                    1234 Main<br>
                                    Apt. 4B<br>
                                    Springfield, ST 54321
                                  </address>
                                </div>
                                <div class="col-md-6 float-right">
                                    <div class="logo float-right">
                                        <a href="http://devlopy.tn" class="simple-text">
                                            <div class="logo-img">
                                              <img width="50" height="50" src="/assets/img/blue.png"/> <b>D E V L O P Y</b>
                                            </div>
                                        </a>
                                    </div>
                                    <address>
                                    <strong>Factur :</strong><br>`;
      if (!this.client.name) {
        printContents += '<div>' + this.client.fname + ' ' + this.client.lname + '</div>'
      } else {
        printContents += '<div>' + this.client.name + '</div>'
      }
      printContents +=    `<div>` + this.client.mat_fis + `</div>
                            ` + this.client.adress + `<br>
                            ` + this.client.tel + `<br>
                            ` + this.client.email + `
                            </address>
                          </div>
                      </div>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-md-12">
                      <div class="panel panel-default">
                        <div class="panel-heading">
                          <h3 class="panel-title"><strong>Rsum du note d'honoraires</strong></h3>
                        </div>
                        <div class="panel-body">
                          <div class="table-responsive">
                            <table class="table table-condensed">
                              <thead>
                                              <tr>
                                    <td><strong>Service</strong></td>
                                    <td class="text-center"><strong></strong></td>
                                    <td class="text-center"><strong></strong></td>
                                    <td class="text-right"><strong>Prix HT</strong></td>
                                    <td class="text-center"><strong></strong></td>
                                              </tr>
                              </thead>
                              <tbody>
                      `;
  
        printContents += `<tr>
        <td>` + services.name + `</td>
        <td class="text-center">
        </td>
        <td class="text-center"></td>
        <td class="text-right">` + services.montant + `</td>
        <td class="text-right"></td>
      </tr>
      <tr>`;
      billTotalht += Number(services.amount.replace(/\s/g, ''));
    
          billTotalttc = billTotalht * 1.3;
          billTotalttc = Number(billTotalttc.toString().replace(/\s/g, '')).toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
          billTotalht = Number(billTotalht.toString().replace(/\s/g, '')).toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
    printContents += ` <td style="border-top: 2px solid;"></td>
        <td style="border-top: 2px solid;"></td>
        <td style="border-top: 2px solid;" class=" text-center"><strong>Total HT</strong></td>
        <td style="border-top: 2px solid;" class="text-right">` + billTotalht + `</td>
      </tr>
      <tr>
        <td style="border-style:none;">`;
        if (services.paid_status) {
          printContents += ` <a>Paye</a>`;
        } else {
          printContents += ` <a>Non Paye</a>`;
        }
        printContents += `</td>
        <td style="border-style:none;"></td>
        <td style="border-style:none;" class="text-center"><strong>TVA</strong></td>
        <td style="border-style:none;" class="text-right">13%</td>
      </tr>
      <tr>
        <td style="border-style:none;">
        </td>
        <td style="border-style:none;"></td>
        <td style="border-style:none;" class="text-center"><strong>Total</strong></td>
        <td style="border-style:none;" class="text-right">` + billTotalttc + `</td>
      </tr>
      </tbody>
      </table>
    </div>
  </div>
</div>
</div>
</div>
</div>`

    popupWin = window.open('', '_blank', 'top=0,left=50%,height=' + screen.height + ',width=' + screen.width);
    popupWin.document.open();
    popupWin.document
    .write(`<html>
     ${$('head').clone().html()}
    <body onload="window.print();window.close()">${printContents}</body></html>`);
    popupWin.document.close();
  }

}
