import { Component, OnInit, Input ,ViewEncapsulation} from '@angular/core';
import {AdminService} from '../../services/admin/admin.service';
import {ManagementService} from '../../services/management/management.service';

 @Component({
  selector: 'app-timesheet',
  templateUrl: './timesheet.component.html',
  styleUrls: ['./timesheet.component.scss'],
})
export class TimesheetComponent implements OnInit {
  @Input() departs : any;
  @Input() users : any;
  @Input() years : any;
  clients = [];
  selectedMonth = new Date().getMonth(); ;
  selectedYear  = new Date().getFullYear();
  result = [];
  services = [];
  data = [];
  selectedDepart = "all";
  selectedClient = "all";
  selectedUser = "all";
  tasks = [];
  totalCharge = [];
  months = ['Janv','Fvr','Mars','Avr','Mai','Juin','Juill','Aot','Sept','Oct','Nov','Dc'];
  constructor(
    private adminService : AdminService,
    private mService : ManagementService,
  ) { 

  }

  ngOnInit() {
    this.setMonths();
    this.getTasks();
    this.getClients();
  }


  getClients(){
    this.adminService.getClients()
    .subscribe(res => {
      this.clients = res ;
      this.getServicesByYear();
  }, err => {
    console.log(err);
  }); 
  }


  getTasks(){
    this.mService.getTasks()
    .subscribe(res => {
      this.tasks = res ;
     // console.log(this.tasks);
  }, err => {
    console.log(err);
  }); 
  }

  checkWeekend(i){
    let d = new Date();
    d.setDate(i);
    d.setMonth(this.selectedMonth);
    return (d.getDay() == 6 || d.getDay() == 0);
  }


  setMonths(){
   let  x = this.selectedMonth + 1;
   this.result = [] ;
    if([1,3,5,7,8,10,12].includes(x)){
      for(let i = 1 ; i < 32 ; i++){
        this.result.push(0);
      }
    }else{
      if(x == 2){
        let x ;
        if ((Number(this.selectedYear)%4==0) && ((Number(this.selectedYear)%100!=0) || (Number(this.selectedYear)%400==0)))
         x = 30 ;
        else
        x = 29 ;
      for(let i = 1 ; i < x ; i++){
        this.result.push(0);
      }
    }else{
      for(let i = 1 ; i < 31 ; i++){
        this.result.push(0);
      }
    }
  }
    
  }

  getServicesByYear(){
    console.log(this.selectedYear);
    this.mService.getServicesByYear(this.selectedYear)
    .subscribe(res => {
    this.services = res ;
 
    this.setTimesheet();
    }, err => {
      console.log(err);
    });
  }


  filterUsers(){
   let x = [];
    this.services.forEach(service => {
      service.tasks = service.tasks.filter(task => {
        if(task.user == this.selectedUser || this.checkHelper(task.helpers))
        return true;
      })
        if(service.tasks.length > 0)       
        x.push(service);
      
    });
    return x ;
  }

  checkHelper(helpers){
    return helpers.findIndex(one => one.user == this.selectedUser) != -1 ;
  }

  setTimesheet(){
    let i = 0 ;
    this.data = [];
    this.totalCharge = this.initArray();
    let clients = this.clients ;
    let newServices = this.services.filter(one => new Date(one.createdAt).getMonth() == this.selectedMonth);
    newServices.forEach(service => {
      service.tasks = service.tasks.filter(one => new Date(one.date_deb).getMonth() == this.selectedMonth);
    });
    let clientIndex=0,serviceIndex=0,modeleTache ;
    if(this.selectedClient != "all")
    clients = this.clients.filter(one => one._id == this.selectedClient);
    if(this.selectedDepart != "all")
    newServices = newServices.filter(one => one.depart == this.selectedDepart);
    if(this.selectedUser != "all")
    newServices = this.filterUsers();
    clients.forEach(client => {
    let cltName : any;
    if(client.pm){
      cltName = client.name;
    }else{
      cltName = client.fname +" "+client.lname;
    }
    this.data.push({ 
      lib : "client",
      title :cltName,
      values :  this.initArray()
    });
    this.data.push({ 
      lib : "client",
      title :cltName+" (Estimation)",
      values :  this.initArray()
    });
    i+=2;
    clientIndex = this.data.findIndex(one => one.title == cltName && one.lib == "client");
    console.log(clientIndex);
   // console.log( this.data.findIndex(one => one.title == cltName && one.lib =="client" ));
    newServices.forEach(service => {
     if(service.client == client._id){
      this.data.push({ 
        ref : cltName,
        lib : "service",
        title :service.name,
        values :  this.initArray()
      });
      this.data.push({ 
        ref : cltName,
        lib : "service",
        title :service.name+" (Estimation)",
        values :  this.initArray()
      });
      i += 2;
      serviceIndex = this.data.findIndex(one => one.title == service.name && one.ref == cltName && one.lib == "service") ;
      console.log(serviceIndex);
      service.tasks.forEach(task => { 
        modeleTache = this.tasks.find(one => one._id == task.task); 
        this.data.push({ 
          lib : "task",
          title : modeleTache.task,
          values :  this.initArray()
        });
        this.data.push({ 
          lib : "task",
          title : modeleTache.task+" (Estimation)",
          values :  this.initArray()
        });
        if(task.helpers && task.helpers.length > 0){
          task.helpers.forEach(help => {
            if(help.timePassed && help.timePassed.length > 0){
            help.timePassed.forEach(one => {
              let index = new Date(one.date).getDate() -1;
              this.data[i].values[index] += Number(one.timePassed);
              this.totalCharge[index] += Number(one.timePassed);
              this.data[clientIndex].values[index] += Number(one.timePassed);
               this.data[serviceIndex].values[index] += Number(one.timePassed);
            });
      
            }
          });
        }

        if(task.timePassed && task.timePassed.length > 0){
          task.timePassed.forEach(one => {
            let index = new Date(one.date).getDate() -1;
            this.data[i].values[index] += Number(one.timePassed);
            this.totalCharge[index] += Number(one.timePassed);
            this.data[clientIndex].values[index] += Number(one.timePassed);
             this.data[serviceIndex].values[index] += Number(one.timePassed);
          });
        }
        let index = new Date(task.date_deb).getDate() -1;
        this.data[i+1].values[index] = Number(modeleTache.time);
        this.data[clientIndex+1].values[index] +=Number(modeleTache.time);
        this.data[serviceIndex+1].values[index] += Number(modeleTache.time);
     
      i+=2;
      });
    }
    });
  });
    console.log("aaaa");
    console.log(this.data);
  }

  initArray(){
    let x = [];
    this.result.forEach(element => {
      x.push(element);
    });

    return x ;
  }

  makeFullScreen(){
    console.log("aaaa");
    let timesheet = document.getElementById('timesheet');
    let table = document.getElementById('timesheetTable');

    if(timesheet.classList.contains('fullScreen')){
      timesheet.classList.remove('fullScreen');
      table.classList.remove('tableFullScreen');
    }else{
    timesheet.classList.add('fullScreen');
    table.classList.add('tableFullScreen');

    }
  }

  checkFullScreen(){
   return document.getElementById('timesheet').classList.contains('fullScreen') ;
  }
}
